/*
 * widget-posts
 */

@import '../variables';


.widget-posts__list {
    list-style: none;
    padding: 0;
    margin: 0 -20px;
}
.widget-posts__item {
    display: flex;
    padding: 15px 20px;
    border-top: 1px solid $card-divider-color;

    &:last-child {
        border-bottom: 1px solid $card-divider-color;
    }
}
.widget-posts__image {
    width: 50px;
    margin-right: 14px;
    flex-shrink: 0;

    img {
        max-width: 100%;
        border-radius: 1.5px;
    }
}
.widget-posts__title {
    font-size: 13px;
    line-height: 15px;
    font-weight: 600;

    a {
        color: inherit;
        transition: color .15s;
    }
    a:hover {
        color: $card-link-hover-color;
        text-decoration: none;
    }
}
.widget-posts__date {
    margin-top: 4px;
    font-size: 12px;
    color: $card-font-muted-color;
}