/*
 * mobilemenu
 */

@import '../variables';


.mobilemenu {}
.mobilemenu__backdrop {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: $mobilemenu-backdrop-bg;
    z-index: 90;
    opacity: 0;
    user-select: none;
    visibility: hidden;
    transition: visibility 0s .2s, opacity .2s;
}
.mobilemenu__container {
    display: flex;
    flex-direction: column;
    width: 280px;
    height: 100%;
    position: fixed;
    background: $mobilemenu-bg;
    color: $mobilemenu-font-color;
    z-index: 100;
    transform: translateX(-280px);
    transition: transform .2s;
}
.mobilemenu--opened {
    .mobilemenu__backdrop {
        visibility: visible;
        opacity: .7;
        transition-delay: 0s, 0s;
    }
    .mobilemenu__container {
        transform: translateX(0);
    }
}
.mobilemenu__header {
    flex-shrink: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 55px;
    border-bottom: 1px solid $mobilemenu-divider-color;
}
.mobilemenu__body {
    flex-grow: 1;
    overflow-y: scroll;
    -webkit-overflow-scrolling: touch;
}
.mobilemenu__title {
    padding: 0 20px;
    font-size: 16px;
    font-weight: 700;
    letter-spacing: -.02em;
}
.mobilemenu__close {
    background-color: transparent;
    border: none;
    width: 54px;
    height: 54px;
    padding: 22px;
    color: $mobilemenu-close-icon-color;
    transition: color .2s;
    cursor: pointer;

    svg {
        fill: currentColor;
        display: block;
    }

    &:hover {
        color: $mobilemenu-close-icon-hover-color;
    }
    &:focus {
        outline: none;
    }
}
.mobilemenu__links {
    list-style: none;
    margin: 0;
    font-size: 14px;
    line-height: 20px;
    font-weight: 600;
}
.mobilemenu__sub-links {
    height: 0;
    opacity: 0;
    overflow: hidden;
    transition: height .2s, opacity .2s;
}
.mobilemenu__divider {
    height: 1px;
    background-color: $mobilemenu-divider-color;
    margin: 10px 0;
}
.mobilemenu__item {
    position: relative;
}
.mobilemenu__link {
    display: block;
    width: 100%;
    border: none;
    text-align: left;
    font-size: inherit;
    font-weight: inherit;
    font-family: inherit;
    line-height: inherit;
    background-color: transparent;
    padding: 7px 44px 7px 20px;
    cursor: pointer;

    &, &:hover {
        color: inherit;
        text-decoration: none;
    }

    &:hover {
        background-color: $mobilemenu-item-hover-bg-color;
    }
    &:focus {
        outline: none;
    }
}
.mobilemenu__link--expander {
    position: relative;
}
.mobilemenu__arrow {
    width: 34px;
    height: 34px;
    position: absolute;
    right: 0;
    top: 0;
    display: block;
    border: none;
    background-color: transparent;
    cursor: pointer;
    padding: 0;
    color: $mobilemenu-arrow-color;

    &:hover {
        background-color: $mobilemenu-item-hover-bg-color;
    }
    &:focus {
        outline: none;
    }

    svg {
        position: absolute;
        left: 14px;
        top: 13px;
        fill: currentColor;
        transition: transform .2s;
    }
}
.mobilemenu__arrow--decorative {
    pointer-events: none;
}


.mobilemenu__counter {
    margin-left: 6px;
    position: relative;
    top: -1px;
    background-color: $accent-color;
    color: $accent-opposite-color;
    display: inline-block;
    padding: 0 5px;
    font-size: 11px;
    line-height: 18px;
    border-radius: 1.5px;
}


.mobilemenu__links--level--1 {
    padding: 10px 0;
}
.mobilemenu__links--level--2 {
    padding: 4px 0 4px;
    color: $mobilemenu-font-alt-muted-color;

    & > .mobilemenu__item > .mobilemenu__link {
        font-size: 13px;
        padding: 6px 44px 6px 40px;
    }
}
.mobilemenu__links--level--3 {
    padding: 4px 0 4px;
    color: $mobilemenu-font-alt-muted-color;

    & > .mobilemenu__item > .mobilemenu__link {
        font-size: 13px;
        padding: 6px 44px 6px 60px;
    }
}

.mobilemenu__item--opened {
    & > .mobilemenu__sub-links {
        height: auto;
        opacity: 1;
    }
    & > .mobilemenu__arrow svg {
        transform: rotateZ(-90deg) translateX(.5px) translateY(.5px);
    }
}


.mobilemenu__value {
    color: $mobilemenu-font-alt-muted-color;
}