/*
 * megamenu
 */

@import '../variables';


.megamenu {
    background: $megamenu-bg;
    box-shadow: $megamenu-shadow;
    color: $megamenu-font-color;
    list-style: none;
    margin: 0;
    padding: 30px;
    width: 1110px;

    a {
        color: inherit;
        transition: color .15s;
    }
    a:hover {
        color: $megamenu-link-hover-color;
        text-decoration: none;
    }
}
.megamenu__links {
    list-style: none;
    padding: 0;
}
.megamenu__links--root {
    & > li {
        line-height: 16px;
        font-size: 14px;
        font-weight: 600;
    }
    & > li + li {
        margin-top: 25px;
    }
}
.megamenu__links--sub {
    margin-top: 14px;
    color: #808080;

    & > li {
        line-height: 15px;
        font-size: 13px;
        font-weight: normal;
    }
    & > li + li {
        margin-top: 8px;
    }
}
.megamenu__banner {
    margin: -15px;
    display: block;
    position: relative;
    overflow: hidden;
    height: 360px;

    img {
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        height: 100%;
    }
}


@media (min-width: 992px) and (max-width: 1199px) {
    .megamenu {
        width: 930px;
    }
}