/*
 * post
 */

@import '../mixins';
@import '../variables';


.post {
    @include card();

    position: relative;
}
.post__featured {
    img {
        display: block;
        max-width: 100%;
    }
}
.post__header {
    padding: 40px 60px 0;
}
.post__header + .post__featured {
    margin-top: 50px;
    margin-bottom: 20px;
}
.post__category {
    font-size: 15px;
    color: $card-font-muted-color;
    margin-bottom: 12px;

    a {
        color: inherit;
        transition: color .15s;
    }
    a:hover {
        color: $card-link-hover-color;
        text-decoration: none;
    }
}
.post__title {
    font-size: 30px;
    font-weight: 700;
    line-height: 1.1333;
    letter-spacing: -.04em;
    backface-visibility: hidden;
}
.post__meta {
    margin-top: 12px;
    font-size: 14px;
    line-height: 18px;
    color: $card-font-muted-color;

    a {
        color: inherit;
        transition: color .15s;
    }
    a:hover {
        color: $card-link-hover-color;
        text-decoration: none;
    }

    .far {
        font-size: 12px;
    }
}
.post__meta-item {

    & + &:before {
        content: '';
        margin: 0 5px;
    }
}
.post__content {
    padding: 35px 60px 30px;
    font-size: 16px;
    line-height: 26px;

    img {
        max-width: 100%;
    }
}
.post__footer {
    padding: 0;
}
.post__footer-content {
    padding-bottom: 40px;
}
.post__tags-share {
    padding: 0 60px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.post__tags {
    color: $card-font-muted-color;
    font-size: 14px;
    line-height: 20px;

    a {
        color: inherit;
        transition: color .15s;
    }
    a:hover {
        color: $card-link-hover-color;
        text-decoration: none;
    }
}

.post__author {
    margin-top: 40px;
    padding: 40px 60px 0;
    border-top: 1px solid $card-divider-color;
    display: flex;
}
.post__author-avatar {
    flex-shrink: 0;
    margin-right: 30px;
    width: 90px;

    img {
        overflow: hidden;
        border-radius: 2000px;
        max-width: 100%;
    }
}
.post__author-name {
    font-size: 15px;
    line-height: 18px;
    font-weight: 600;
    margin-bottom: 4px;

    a {
        color: inherit;
        transition: color .15s;
    }
    a:hover {
        color: $card-link-hover-color;
        text-decoration: none;
    }
}
.post__author-description {
    font-size: 14px;
    line-height: 22px;
}
.post__author-socials {
    padding: 0;
    margin: 6px 0 0;
    list-style: none;
    display: flex;
    color: $card-font-alt-color;

    a {
        display: block;
        width: 16px;
        height: 16px;
        font-size: 12px;
        line-height: 16px;
        text-align: center;
        margin-right: 6px;
    }
    
    a {
        color: inherit;
        transition: color .15s;
    }
    a:hover {
        color: $card-link-hover-color;
        text-decoration: none;
    }
}



@media (min-width: 768px) and (max-width: 991px) {
    .post__header {
        padding-left: 50px;
        padding-right: 50px;
    }
    .post__header + .post__featured {
        margin-top: 40px;
        margin-bottom: 10px;
    }
    .post__content {
        padding-left: 50px;
        padding-right: 50px;
        padding-bottom: 20px;
    }
    .post__tags-share {
        padding-left: 50px;
        padding-right: 50px;
    }
    .post__author {
        padding-left: 50px;
        padding-right: 50px;
    }
}
@media (min-width: 576px) and (max-width: 767px) {
    .post__header {
        padding-top: 30px;
        padding-left: 30px;
        padding-right: 30px;
    }
    .post__header + .post__featured {
        margin-top: 35px;
        margin-bottom: 0;
    }
    .post__content {
        padding: 25px 30px 10px;
    }
    .post__tags-share {
        padding-left: 30px;
        padding-right: 30px;
    }
    .post__footer-content {
        padding-bottom: 30px;
    }
    .post__author {
        padding-top: 30px;
        padding-left: 30px;
        padding-right: 30px;
    }
    .post__author-avatar {
        margin-right: 20px;
    }
}
@media (max-width: 575px) {
    .post__header {
        padding-top: 15px;
        padding-left: 15px;
        padding-right: 15px;
    }
    .post__header + .post__featured {
        margin-top: 20px;
        margin-bottom: 0;
    }

    .post__content {
        padding: 15px 15px 10px;
    }
    .post__tags-share {
        display: block;
        padding-left: 15px;
        padding-right: 15px;
    }
    .post__tags {
        margin-bottom: 12px;
    }
    .post__footer-content {
        padding-bottom: 15px;
    }
    .post__author {
        text-align: center;
        display: block;
        margin-top: 20px;
        padding-top: 15px;
        padding-left: 15px;
        padding-right: 15px;
    }
    .post__author-avatar {
        margin-right: 0;
        margin-bottom: 10px;
        width: auto;

        img {
            width: 90px;
        }
    }
    .post__author-socials {
        justify-content: center;
    }
}