/*
 * reviews
 */

@import '../variables';


.review {
    display: flex;
}
.review__avatar {
    flex-shrink: 0;
    margin-right: 22px;

    img {
        width: 70px;
        border-radius: 1000px;
    }
}
.review__author {
    font-size: 15px;
    line-height: 1;
    font-weight: 600;
}
.review__rating {
    margin-top: 8px;
}
.review__text {
    margin-top: 8px;
    font-size: 15px;
    line-height: 22px;
}
.review__date {
    margin-top: 5px;
    font-size: 13px;
    color: $card-font-muted-color;
}

@media (min-width: 576px) and (max-width: 767px) {
    .review__avatar {
        margin-right: 18px;

        img {
            width: 60px;
        }
    }
}
@media (max-width: 575px) {
    .review__avatar {
        display: none;
    }
}