/*
 * base
 */

@import '../variables';
@import '../mixins';


html, body {
    height: 100%;
}
html {
    overflow-x: hidden;
    overflow-y: scroll;
}


body {
    background: $body-bg;
    color: $body-font-color;

    font-family: $base-font-family;
    font-size: $base-font-size;
    font-weight: $base-font-weight;
    line-height: $base-line-height;
}


a {
    color: $body-link-color;
}
a:hover {
    color: $body-link-hover-color;
}


h1 {
    @include header-h1();
}
h2 {
    @include header-h2();
}
h3 {
    @include header-h3();
}
h4 {
    @include header-h4();
}
h5 {
    @include header-h5();
}
h6 {
    @include header-h6();
}