/*
 * footer-links
 */

@import '../mixins';


.footer-links__list {
    font-size: 14px;
    line-height: 18px;
    list-style: none;
    padding: 0;
    margin: 0;
}
.footer-links__link {
    color: $footer-font-alt-color;

    &:hover {
        color: $footer-link-hover-color;
    }

    &, &:hover {
        text-decoration: none;
    }
}

@media (min-width: 768px) {
    .footer-links__link {
        transition: color .15s;
    }
    .footer-links__header {
        @include decor-header();
        @include decor-header-h4();

        margin-bottom: 28px;
    }
    .footer-links__header-arrow {
        display: none;
    }
    .footer-links__item {
        padding: 3px 0 4px;
    }
}

@media (max-width: 767px) {
    .footer-links {
        color: $footer-details-font-color;
        background-color: $footer-details-bg-color;
        box-shadow: $footer-details-shadow;
        margin-bottom: 20px;
        border-radius: $footer-links-border-radius;
        overflow: hidden;
    }
    .footer-links__header {
        position: relative;
        font-size: 15px;
        line-height: 19px;
        font-weight: 600;
        padding: 12px 35px 12px 15px;
        margin-bottom: 0;
        cursor: pointer;
        user-select: none;

        &:hover {
            background-color: $footer-details-hover-bg-color;
        }
    }
    .footer-links__header-arrow {
        position: absolute;
        top: 18px;
        right: 13px;
        transition: transform .2s;
        fill: $footer-details-arrow-color;
    }
    .footer-links__content {
        overflow: hidden;
        height: 0;
        opacity: 0;
        transition: height .2s, opacity .2s;
    }
    .footer-links__list {
        padding: 10px 0;
        border-top: 1px solid $footer-details-divider-color;
    }
    .footer-links__link {
        display: block;
        padding: 6px 15px;
        color: $footer-links-font-color;

        &:hover {
            background-color: $footer-links-hover-bg-color;;
            color: $footer-links-hover-font-color;
        }
    }

    .footer-links--opened {
        .footer-links__header-arrow {
            transform: rotateZ(90deg) translateX(.5px) translateY(.5px);
        }
        .footer-links__content {
            height: auto;
            opacity: 1;
        }
    }
}