@mixin header-h6() {
    font-family: $h6-font-family;
    font-size: $h6-font-size;
    font-weight: $h6-font-weight;
    font-style: $h6-font-style;
    letter-spacing: $h6-letter-spacing;
    line-height: $h6-line-height;
    text-transform: $h6-text-transform;
}

@mixin decor-header() {
    position: relative;
    z-index: 0;
    backface-visibility: hidden;
    margin: 0;

    &:after {
        content: '';
        display: block;
        height: 2px;
        background-color: $decor-header-underline-color;
        position: absolute;
        left: 0;
        bottom: 0;
        z-index: -1;
    }
}
@mixin decor-header-h1() {
    @include header-h1();

    padding-bottom: 5px;

    &:after {
        width: 60px;
    }
}
@mixin decor-header-h2() {
    @include header-h2();

    padding-bottom: 5px;

    &:after {
        width: 50px;
    }
}
@mixin decor-header-h3() {
    @include header-h3();

    padding-bottom: 5px;

    &:after {
        width: 40px;
    }
}

@mixin decor-header-h4() {
    @include header-h4();

    padding-bottom: 5px;

    &:after {
        width: 35px;
    }
}
@mixin decor-header-align-center() {
    text-align: center;

    &:after {
        left: 50%;
        transform: translateX(-50%);
    }
}



@mixin button($bg-color, $font-color, $hover-bg-color, $hover-font-color, $active-bg-color, $active-font-color) {
    // default
    background-color: $bg-color;
    color: $font-color;

    // hover
    &:hover {
        background-color: $hover-bg-color;
        color: $hover-font-color;
    }

    // focus
    &.focus,
    &:focus {
        background-color: $hover-bg-color;
        color: $hover-font-color;
    }

    // active
    &:not(:disabled):not(.disabled).active,
    &:not(:disabled):not(.disabled):active {
        background-color: $active-bg-color;
        color: $active-font-color;
    }

    // disabled
    &.disabled,
    &:disabled {
        background-color: $bg-color;
        color: $font-color;
        opacity: .65;
    }
}