/*
 * widget-filters
 */

@import '../variables';


.widget-filters__filter {
    margin: 0 -20px;
    border-top: 1px solid $card-divider-color;
}
.widget-filters__filter:last-child {
    border-bottom: 1px solid $card-divider-color;
}
.widget-filters__filter-name {
    font-size: 14px;
    font-weight: 600;
    padding: 11px 20px 4px;
}
.widget-filters__filter-content {
    padding: 6px 0 22px;
    margin: 0 20px;
}
.widget-filters__actions {
    padding-top: 18px;
}