/*
 * view-options
 */

@import '../mixins';

.view-options {
    @include card();

    padding: 9px 12px;
    font-size: 13px;
    font-weight: 600;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
}
.view-options__layout {
    display: flex;
}
.view-options__layout-button {
    background-color: transparent;
    border: none;
    fill: currentColor;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0;
    width: 28px;
    height: 28px;
    cursor: pointer;
    border-radius: 2px;

    & {
        color: $layout-button-color;
    }
    &:hover {
        color: $layout-button-hover-color;
    }

    &:focus {
        outline: none;
    }
}
.view-options__layout-button:not(.view-options__layout-button--active):hover {
    background-color: $layout-button-hover-bg-color;
}
.view-options__layout-button--active {
    &, &:hover {
        color: $layout-button-active-color;
        cursor: default;
    }
}
.view-options__legend {
    margin-left: 20px;
}
.view-options__divider {
    flex-grow: 1;
}
.view-options__control {
    display: flex;
    align-items: center;
    margin-left: 25px;
}
.view-options__control-label {
    margin-right: 8px;
    margin-bottom: 0;
    font-size: inherit;
}


@media (max-width: 767px) {
    .view-options {
        justify-content: flex-end;
    }
    .view-options__legend {
        margin-left: auto;
    }
    .view-options__divider {
        width: 100%;
        height: 1px;
        margin: 9px -12px;
        background-color: $card-divider-color;
    }
}
@media (max-width: 459px) {
    .view-options {
        justify-content: flex-start;
    }
    .view-options__control {
        display: block;
        margin-left: 0;
        margin-right: 15px;
    }
    .view-options__control-label {
        margin-right: 0;
        margin-bottom: 4px;
    }
}