/*
 * alert
 */
.alert {
  background-color: #fff;
  box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.02), 0 1px 4px #cfdee5;
  border: none;
  border-radius: 0;
  font-size: 14px;
  line-height: 18px;
  padding: 12px 20px;
}

.alert::before {
  content: '';
  display: block;
  position: absolute;
  border-radius: 1.2px;
  top: 3px;
  left: 3px;
  width: 5px;
  height: calc(100% - 6px);
}

.alert a {
  border-radius: 1.5px;
  color: inherit;
  padding: 0 3px;
  background-color: currentColor;
  border-bottom: 1px solid transparent;
  transition: border-bottom-color .12s;
}

.alert a:hover {
  color: inherit;
  text-decoration: none;
}

.alert-dismissible {
  padding-right: 42px;
}

.alert__close-button {
  position: absolute;
  top: 0;
  right: 0;
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
  width: 42px;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  fill: #b3b3b3;
  transition: fill .12s;
}

.alert__close-button:hover {
  fill: #737373;
}

.alert__close-button:focus {
  outline: none;
}

.alert__close-button svg {
  display: block;
}

.alert-sm {
  padding: 7px 17px;
}

.alert-sm.alert-dismissible {
  padding-right: 32px;
}

.alert-sm .alert__close-button {
  width: 32px;
}

.alert-lg {
  padding: 15px 24px;
  font-size: 15px;
}

.alert-lg.alert-dismissible {
  padding-right: 48px;
}

.alert-lg .alert__close-button {
  width: 48px;
}

.alert-primary {
  color: #4274a6;
}

.alert-primary::before {
  background-color: #7eb2e6;
}

.alert-primary a {
  background-color: rgba(66, 116, 166, 0.15);
}

.alert-primary a:hover {
  border-bottom-color: rgba(66, 116, 166, 0.5);
}

.alert-primary a:active {
  background-color: rgba(66, 116, 166, 0.25);
}

.alert-success {
  color: #359a21;
}

.alert-success::before {
  background-color: #87bf7c;
}

.alert-success a {
  background-color: rgba(53, 154, 33, 0.15);
}

.alert-success a:hover {
  border-bottom-color: rgba(53, 154, 33, 0.5);
}

.alert-success a:active {
  background-color: rgba(53, 154, 33, 0.25);
}

.alert-danger {
  color: #bf2525;
}

.alert-danger::before {
  background-color: #e67e7e;
}

.alert-danger a {
  background-color: rgba(191, 37, 37, 0.15);
}

.alert-danger a:hover {
  border-bottom-color: rgba(191, 37, 37, 0.5);
}

.alert-danger a:active {
  background-color: rgba(191, 37, 37, 0.25);
}

.alert-warning {
  color: #cd8e0e;
}

.alert-warning::before {
  background-color: #e5b85c;
}

.alert-warning a {
  background-color: rgba(205, 142, 14, 0.15);
}

.alert-warning a:hover {
  border-bottom-color: rgba(205, 142, 14, 0.5);
}

.alert-warning a:active {
  background-color: rgba(205, 142, 14, 0.25);
}

/*
 * breadcrumb
 */
.breadcrumb {
  background-color: transparent;
  font-size: 12px;
  font-weight: 600;
  margin: 0;
  padding: 14px 0;
}

.breadcrumb a {
  color: #8a9499;
  transition: color .15s;
}

.breadcrumb a:hover {
  color: #f26924;
}

.breadcrumb a, .breadcrumb a:hover {
  text-decoration: none;
}

.breadcrumb-item.active {
  color: #8a9499;
}

.breadcrumb-item + .breadcrumb-item {
  padding-left: 11px;
}

.breadcrumb-item + .breadcrumb-item::before {
  padding-right: 11px;
  color: #8a9499;
  opacity: .5;
}

@media (max-width: 991px) {
  .breadcrumb {
    justify-content: center;
  }
}

/*
 * button-remove
 */
.button-remove {
  background-color: transparent;
  border: none;
  width: 24px;
  height: 24px;
  padding: 7px;
  color: #b3b3b3;
  transition: color .2s;
  cursor: pointer;
}

.button-remove svg {
  fill: currentColor;
  display: block;
}

.button-remove:hover {
  color: #ff4040;
}

.button-remove:focus {
  outline: none;
}

.button-remove--lg {
  width: 32px;
  height: 32px;
  padding: 11px;
}

/*
 * button
 */
.btn {
  border-radius: 1.5px;
  fill: currentColor;
  line-height: 1;
  font-weight: 600;
  transition: color .1s linear, background-color .1s linear, border-color .1s linear, box-shadow .1s linear;
  /* remove border and shadow */
}

.btn svg {
  vertical-align: middle;
}

.btn, .btn.focus, .btn:focus, .btn.focus:hover, .btn:focus:hover, .btn:not(:disabled):not(.disabled).active:focus, .btn:not(:disabled):not(.disabled):active:focus {
  border: none;
  box-shadow: none;
}

/* md size - 36 (default) */
.btn {
  font-size: 0.875rem;
  padding: 0.78571em 1.35714em;
}

/* lg size - 42 */
.btn-lg {
  font-size: 0.9375rem;
  padding: 0.9em 1.73333em;
}

/* sm size - 28 */
.btn-sm {
  font-size: 0.8125rem;
  padding: 0.57692em 1.07692em;
}

/* xs size - 24 */
.btn-xs {
  font-size: 0.75rem;
  padding: 0.5em 0.91667em;
}

/* styles */
.btn-primary {
  background-color: #ffd599;
  color: #333;
}

.btn-primary:hover {
  background-color: #576166;
  color: #fff;
}

.btn-primary.focus, .btn-primary:focus {
  background-color: #576166;
  color: #fff;
}

.btn-primary:not(:disabled):not(.disabled).active, .btn-primary:not(:disabled):not(.disabled):active {
  background-color: #363d40;
  color: #fff;
}

.btn-primary.disabled, .btn-primary:disabled {
  background-color: #ffd599;
  color: #333;
  opacity: .65;
}

.btn-secondary {
  background-color: #cfdee5;
  color: #4d4d4d;
}

.btn-secondary:hover {
  background-color: #576166;
  color: #fff;
}

.btn-secondary.focus, .btn-secondary:focus {
  background-color: #576166;
  color: #fff;
}

.btn-secondary:not(:disabled):not(.disabled).active, .btn-secondary:not(:disabled):not(.disabled):active {
  background-color: #363d40;
  color: #fff;
}

.btn-secondary.disabled, .btn-secondary:disabled {
  background-color: #cfdee5;
  color: #4d4d4d;
  opacity: .65;
}

.btn-light {
  background-color: transparent;
  color: #ccc;
}

.btn-light:hover {
  background-color: #e6eef2;
  color: #4d4d4d;
}

.btn-light.focus, .btn-light:focus {
  background-color: #e6eef2;
  color: #4d4d4d;
}

.btn-light:not(:disabled):not(.disabled).active, .btn-light:not(:disabled):not(.disabled):active {
  background-color: #cfdee5;
  color: #4d4d4d;
}

.btn-light.disabled, .btn-light:disabled {
  background-color: transparent;
  color: #ccc;
  opacity: .65;
}

/* svg icon */
.btn-svg-icon {
  position: relative;
  padding: 0;
}

.btn-svg-icon svg {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translateX(-50%) translateY(-50%);
}

.btn-svg-icon {
  width: 36px;
  height: 36px;
}

.btn-svg-icon.btn-lg {
  width: 42px;
  height: 42px;
}

.btn-svg-icon.btn-sm {
  width: 28px;
  height: 28px;
}

.btn-svg-icon.btn-xs {
  width: 24px;
  height: 24px;
}

/* loading state */
@keyframes btn-loading-animation {
  from {
    transform: translateX(-50%) translateY(-50%) rotateZ(0deg);
  }
  to {
    transform: translateX(-50%) translateY(-50%) rotateZ(360deg);
  }
}

.btn-loading {
  position: relative;
  /* hide button content */
  /* md size (default) */
  /* lg size */
  /* sm size */
  /* xs size */
}

.btn-loading:after {
  display: block;
  content: '';
  position: absolute;
  left: 50%;
  top: 50%;
  border: 2px solid rgba(0, 0, 0, 0.1);
  border-top-color: rgba(0, 0, 0, 0.5);
  border-radius: 1000px;
  transition: border-color .1s;
  transform: translateX(-50%) translateY(-50%);
  animation-name: btn-loading-animation;
  animation-duration: .5s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
}

.btn-loading, .btn-loading:hover, .btn-loading.focus, .btn-loading:focus, .btn-loading:not(:disabled):not(.disabled).active, .btn-loading:not(:disabled):not(.disabled):active, .btn-loading.disabled, .btn-loading:disabled {
  color: rgba(0, 0, 0, 0);
}

.btn-loading:after {
  width: 22px;
  height: 22px;
}

.btn-loading.btn-lg:after {
  width: 26px;
  height: 26px;
}

.btn-loading.btn-sm:after {
  width: 16px;
  height: 16px;
}

.btn-loading.btn-xs:after {
  width: 14px;
  height: 14px;
}

