  font-size: 15px;
  line-height: 21px;
  font-weight: 600;
  font-family: inherit;
  color: inherit;
  letter-spacing: -.02em;
  width: 100%;
  background-color: transparent;
  border: none;
  text-align: left;
  cursor: pointer;
  fill: #999;
  position: relative;
}

.toggle__title button:hover {
