}

.toggle__title button:focus {
  outline: none;
}

.toggle__title button svg {
  position: absolute;
  top: calc(50% - 5px);
  right: 24px;
  transition: transform .2s;
}

.toggle__body {
  overflow: hidden;
  height: 0;
  opacity: 0;
  transition: height .2s, opacity .2s;
}

.toggle__content {
  border-top: 1px solid #ebebeb;
  padding: 20px 22px;
