  }
}

@media (max-width: 767px) {
  .cart__table {
    display: block;
  }
  .cart__header {
    display: none;
  }
  .cart__body {
    display: block;
  }
  .cart__body tr {
    background-color: #fff;
    box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.02), 0 1px 4px #cfdee5;
    position: relative;
    display: block;
    padding: 20px;
    margin-bottom: 20px;
  }
  .cart__footer {
    display: block;
  }
  .cart__footer tr {
    background-color: #fff;
    box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.02), 0 1px 4px #cfdee5;
    display: flex;
    justify-content: space-between;
    padding: 15px;
    margin-bottom: 20px;
  }
  .cart__column {
    display: block;
  }
  .cart__column--image {
    text-align: center;
    margin-bottom: 20px;
  }
  .cart__column--image img {
    max-width: 200px;
  }
  .cart__column--info {
    margin: 0 -20px;
    text-align: center;
    border-bottom: 1px solid #ebebeb;
    padding: 0 20px 18px;
    margin-bottom: 12px;
  }
  .cart__column--price,
  .cart__column--total {
    font-weight: 600;
  }
  .cart__column--price,
  .cart__column--quantity,
  .cart__column--total {
    padding: 6px 0;
    display: flex;
    align-items: center;
    text-align: right;
    justify-content: space-between;
  }
  .cart__column--price:before,
  .cart__column--quantity:before,
  .cart__column--total:before {
    text-align: left;
    font-weight: 600;
    width: 55%;
    flex-shrink: 0;
    content: attr(data-title) ": ";
  }
  .cart__column--remove {
    background-color: #fff;
    padding: 6px;
    position: absolute;
    right: 0;
    top: 0;
  }
}

