/*
 * header
 */
.header {
  border-bottom: 1px solid #cfdee6;
  background-color: transparent;
  color: inherit;
}

.header__body {
  height: 70px;
  width: 1110px;
  margin: 0 auto;
  display: flex;
  position: relative;
  z-index: 10;
}

.header__mobilemenu {
  display: none;
  border: none;
  background-color: transparent;
  cursor: pointer;
  padding: 0 22px;
  fill: #a1adb2;
}

.header__mobilemenu svg {
  display: block;
}

.header__mobilemenu:hover {
  background-color: rgba(0, 0, 0, 0.04);
  fill: #a1adb2;
}

.header__mobilemenu:focus {
  background-color: rgba(0, 0, 0, 0.04);
  fill: #a1adb2;
  outline: none;
}

.header__logo {
  display: flex;
  align-items: center;
}

