  color: inherit;
}

.header__logo img,
.header__logo svg {
  display: block;
}

.header__nav {
  margin-left: 30px;
}

.header__spring {
  flex-grow: 1;
}

.header__indicator {
  position: relative;
}

.header__indicator-button {
  height: 100%;
  border: none;
  background-color: transparent;
  display: flex;
  align-items: center;
  cursor: pointer;
  padding: 0 6px;
}

.header__indicator-button, .header__indicator-button:hover {
  color: inherit;
}

.header__indicator-dropdown {
  position: absolute;
  right: 0;
  top: 100%;
  visibility: hidden;
  transform-origin: top;
  transform: rotateX(45deg);
  opacity: 0;
  transition: transform .2s, opacity .2s;
}

.header__indicator:hover .header__indicator-button,
.header__indicator--opened .header__indicator-button {
  background-color: rgba(0, 0, 0, 0.04);
}

.header__indicator[data-dropdown-trigger="hover"]:hover .header__indicator-dropdown,
.header__indicator[data-dropdown-trigger="click"].header__indicator--opened .header__indicator-dropdown {
  opacity: 1;
  visibility: visible;
  transform: rotateX(0deg);
}

