  line-height: 16px;
  font-size: 14px;
  font-weight: 600;
}

.megamenu__links--root > li + li {
  margin-top: 25px;
}

.megamenu__links--sub {
  margin-top: 14px;
  color: #808080;
}

.megamenu__links--sub > li {
  line-height: 15px;
  font-size: 13px;
  font-weight: normal;
}

.megamenu__links--sub > li + li {
  margin-top: 8px;
}

.megamenu__banner {
  margin: -15px;
  display: block;
  position: relative;
  overflow: hidden;
  height: 360px;
}

.megamenu__banner img {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  height: 100%;
}

@media (min-width: 992px) and (max-width: 1199px) {
  .megamenu {
    width: 930px;
  }
}

/*
 * menu
 */
.menu {
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.1), 0 2px 15px rgba(0, 0, 0, 0.15);
  background: #fff;
  color: #333;
  list-style: none;
  margin: 0;
  padding: 12px 0;
  width: 190px;
}

.menu__item {
  position: relative;
}

.menu__sub {
  position: absolute;
  left: 100%;
  top: -12px;
  visibility: hidden;
  transform-origin: left;
  transform: rotateY(45deg);
  opacity: 0;
  transition: transform .2s, opacity .2s;
}

.menu__link {
  display: flex;
  justify-content: space-between;
  padding: 7px 20px;
  font-size: 13px;
  line-height: 15px;
  font-weight: 600;
}

.menu__link, .menu__link:hover {
  color: inherit;
  text-decoration: none;
}

.menu__link-arrow {
  flex-shrink: 0;
  align-self: center;
  fill: rgba(0, 0, 0, 0.3);
  margin-right: -2px;
  margin-bottom: 1px;
  margin-left: 10px;
}

.menu__item:hover > .menu__link {
  background-color: #e6eef2;
}

.menu__item:hover > .menu__sub {
  opacity: 1;
  visibility: visible;
  transform: rotateY(0deg);
}

/*
 * mobilemenu
 */
.mobilemenu__backdrop {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: #333;
  z-index: 90;
  opacity: 0;
  user-select: none;
  visibility: hidden;
  transition: visibility 0s .2s, opacity .2s;
}

.mobilemenu__container {
  display: flex;
  flex-direction: column;
  width: 280px;
  height: 100%;
  position: fixed;
  background: #fff;
  color: #333;
  z-index: 100;
  transform: translateX(-280px);
  transition: transform .2s;
}

.mobilemenu--opened .mobilemenu__backdrop {
  visibility: visible;
  opacity: .7;
  transition-delay: 0s, 0s;
}

.mobilemenu--opened .mobilemenu__container {
  transform: translateX(0);
}

.mobilemenu__header {
  flex-shrink: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 55px;
  border-bottom: 1px solid #ebebeb;
}

.mobilemenu__body {
  flex-grow: 1;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
}

.mobilemenu__title {
  padding: 0 20px;
  font-size: 16px;
  font-weight: 700;
  letter-spacing: -.02em;
}

.mobilemenu__close {
  background-color: transparent;
  border: none;
  width: 54px;
  height: 54px;
  padding: 22px;
  color: #b3b3b3;
  transition: color .2s;
  cursor: pointer;
}

.mobilemenu__close svg {
  fill: currentColor;
  display: block;
}

.mobilemenu__close:hover {
  color: #4d4d4d;
}

.mobilemenu__close:focus {
  outline: none;
}

.mobilemenu__links {
  list-style: none;
  margin: 0;
  font-size: 14px;
  line-height: 20px;
  font-weight: 600;
}

.mobilemenu__sub-links {
  height: 0;
  opacity: 0;
  overflow: hidden;
  transition: height .2s, opacity .2s;
}

.mobilemenu__divider {
  height: 1px;
  background-color: #ebebeb;
  margin: 10px 0;
}

.mobilemenu__item {
  position: relative;
}

.mobilemenu__link {
  display: block;
  width: 100%;
  border: none;
  text-align: left;
  font-size: inherit;
  font-weight: inherit;
  font-family: inherit;
  line-height: inherit;
  background-color: transparent;
  padding: 7px 44px 7px 20px;
  cursor: pointer;
}

.mobilemenu__link, .mobilemenu__link:hover {
  color: inherit;
  text-decoration: none;
}

.mobilemenu__link:hover {
  background-color: #f2f2f2;
}

.mobilemenu__link:focus {
  outline: none;
}

.mobilemenu__link--expander {
  position: relative;
}

.mobilemenu__arrow {
  width: 34px;
  height: 34px;
  position: absolute;
  right: 0;
  top: 0;
  display: block;
  border: none;
  background-color: transparent;
  cursor: pointer;
  padding: 0;
  color: #b3b3b3;
}

.mobilemenu__arrow:hover {
  background-color: #f2f2f2;
}

.mobilemenu__arrow:focus {
  outline: none;
}

.mobilemenu__arrow svg {
  position: absolute;
  left: 14px;
  top: 13px;
  fill: currentColor;
  transition: transform .2s;
}

.mobilemenu__arrow--decorative {
  pointer-events: none;
}

.mobilemenu__counter {
  margin-left: 6px;
  position: relative;
  top: -1px;
  background-color: #ffd599;
  color: #333;
  display: inline-block;
  padding: 0 5px;
  font-size: 11px;
  line-height: 18px;
  border-radius: 1.5px;
}

.mobilemenu__links--level--1 {
  padding: 10px 0;
}

.mobilemenu__links--level--2 {
  padding: 4px 0 4px;
  color: #7e888c;
}

.mobilemenu__links--level--2 > .mobilemenu__item > .mobilemenu__link {
  font-size: 13px;
  padding: 6px 44px 6px 40px;
}

.mobilemenu__links--level--3 {
  padding: 4px 0 4px;
  color: #7e888c;
}

.mobilemenu__links--level--3 > .mobilemenu__item > .mobilemenu__link {
  font-size: 13px;
  padding: 6px 44px 6px 60px;
}

.mobilemenu__item--opened > .mobilemenu__sub-links {
  height: auto;
  opacity: 1;
}

.mobilemenu__item--opened > .mobilemenu__arrow svg {
  transform: rotateZ(-90deg) translateX(0.5px) translateY(0.5px);
}

.mobilemenu__value {
  color: #7e888c;
}

/*
 * search
 */
.search {
  position: absolute;
  height: 100%;
  width: 100%;
  background: #fff;
  color: #333;
  z-index: 100;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.1), 0 2px 15px rgba(0, 0, 0, 0.15);
  opacity: 0;
  transform-origin: top center;
  transform: translateY(-100%);
  transition: opacity .2s, transform .2s;
}

.search--opened {
  opacity: 1;
  transform: translateY(0);
}

.search__form {
  display: flex;
  height: 100%;
}

.search__input {
  width: 1px;
  flex-basis: 0;
  background-color: transparent;
  border: none;
  padding: 0 30px 4px;
  margin: 0;
  flex-grow: 1;
  font-family: inherit;
  font-size: 22px;
  letter-spacing: -.02em;
  font-weight: 600;
  color: inherit;
}

.search__input:focus {
  outline: none;
}

.search__input::placeholder {
  color: #b2b2b2;
}

.search__button {
  flex-shrink: 0;
  background-color: transparent;
  border: none;
  padding: 0;
  margin: 0;
  width: 70px;
  color: #b2b2b2;
  position: relative;
  cursor: pointer;
  transition: color .2s;
}

.search__button svg {
  position: absolute;
  left: calc(50% - 10px);
  top: calc(50% - 10px);
  fill: currentColor;
}

.search__button:focus {
  outline: none;
}

.search__button:hover {
  color: #4d4d4d;
}

.search__button + .search__button {
  border-left: 1px solid #ebebeb;
}

@media (max-width: 575px) {
  .search__input {
    font-size: 18px;
    padding: 0 18px 2px;
  }
  .search__button {
    width: 54px;
  }
}

