/*
 * share-links
 */

.share-links {}
.share-links__list {
    display: flex;
    list-style: none;
    padding: 0;
    margin: 0;
    font-size: 11px;
    line-height: 1;
    font-weight: 600;
    color: #fff;
    white-space: nowrap;
}
.share-links__item {
    & + & {
        margin-left: 4px;
    }
}
.share-links__item a {
    border-radius: 2px;
    display: block;
    padding: 4px 7px 5px 7px;
    color: #fff;

    &, &:hover {
        color: inherit;
        text-decoration: none;
    }
}
.share-links__item--type--like a {
    background-color: #4267b2;

    &:hover {
        background-color: darken(#4267b2, 5%);
    }
}
.share-links__item--type--tweet a {
    background-color: #1b95e0;

    &:hover {
        background-color: darken(#1b95e0, 5%);
    }
}
.share-links__item--type--pin a {
    background-color: #e63939;

    &:hover {
        background-color: darken(#e63939, 5%);
    }
}
.share-links__item--type--counter a {
    color: #737373;
    box-shadow: inset 0 0 0 1px rgba(#000, .2);
}