/*
 * spec
 */

@import '../variables';


.spec {
    font-size: 14px;
    line-height: 17px;
}
.spec__header {
    margin-bottom: 45px;
}
.spec__section {
    & + & {
        margin-top: 45px;
    }
}
.spec__section-title {
    font-weight: 600;
    font-size: 18px;
    letter-spacing: -.03em;
    line-height: 20px;
    margin: 0 0 15px;
}
.spec__row {
    padding: 8px 12px;
    display: flex;
    border-bottom: 1px solid $card-divider-color;

    &:first-of-type {
        border-top: 1px solid $card-divider-color;
    }
}
.spec__name {
    width: 230px;
    flex-shrink: 0;
    padding-right: 30px;
    color: $card-font-muted-color;
}
.spec__disclaimer {
    margin-top: 40px;
    font-size: 12px;
    line-height: 20px;
    color: $card-font-muted-color;
}

@media (min-width: 576px) and (max-width: 767px) {
    .spec__name {
        width: 170px;
    }
}
@media (max-width: 575px) {
    .spec__row {
        display: block;
        padding: 12px 0;
    }
    .spec__name {
        width: auto;
        margin-bottom: 6px;
    }
}