/*
 * comments
 */

@import '../variables';


$avatar-size: 40px;
$avatar-size-md: 70px;


/* comments-list */
.comments-list {
    margin: 0 -25px;
}
.comments-list__items {
    padding: 0;
    margin: 0;
    list-style: none;
}
.comments-list__item {
    border-top: 1px solid $card-divider-color;

    &:last-child {
        border-bottom: 1px solid $card-divider-color;
    }
}
.comments-list__pagination {
    margin-top: 25px;
}
.comments-list__items--sub {
    .comments-list__item {
        padding-left: (25px + $avatar-size-md / 2);
    }
    .comments-list__item:last-child {
        border-bottom: none;
    }
}



/* comment */
.comment {
    display: flex;
    font-size: 15px;
    padding: 25px 25px;
}
.comment__avatar {
    margin-right: 22px;
    flex-shrink: 0;

    img {
        width: $avatar-size-md;
        border-radius: 1000px;
    }
}
.comment__content {
    flex-grow: 1;
}
.comment__reply {
    float: right;
}
.comment__author {
    font-weight: 600;
    line-height: 18px;
    margin-bottom: 2px;

    a {
        color: inherit;
        transition: color .15s;
    }
    a:hover {
        color: $card-link-hover-color;
        text-decoration: none;
    }
}
.comment__date {
    font-size: 13px;
    color: $card-font-muted-color;
    margin-bottom: 8px;
}
.comment__text {
    line-height: 22px;
}



@media (max-width: 767px) {
    .comments-list__items--sub {
        .comments-list__item {
            padding-left: (25px + $avatar-size / 2);
        }
    }

    .comment__avatar {
        margin-right: 15px;

        img {
            width: $avatar-size;
        }
    }
    .comment__text {
        margin-top: 12px;
        margin-left: -(15px + $avatar-size);
    }
}
@media (max-width: 459px) {
    .comments-list__items--sub {
        .comments-list__item {
            padding-left: (30px);
        }
    }

    .comment {
        padding: 15px 25px;
    }
    .comment__avatar {
        display: none;
    }
    .comment__author {
        font-size: 13px;
    }
    .comment__date {
        margin-bottom: 6px;
    }
    .comment__text {
        margin-top: 0;
        margin-left: 0;
        font-size: 14px;
    }
}