/*
 * posts-list
 */

.posts-list {
    display: flex;
    flex-wrap: wrap;
}
.posts-list__item {
    flex-shrink: 0;
}

.posts-list--layout--list {
    $margin: 10px;
    $columns: 1;

    margin: (-$margin);

    .posts-list__item {
        margin: $margin;
        width: calc(#{100% / $columns} - #{$margin * 2});
    }
}

.posts-list--layout--grid-2 {
    $margin: 10px;
    $columns: 2;

    margin: (-$margin);

    .posts-list__item {
        margin: $margin;
        width: calc(#{100% / $columns} - #{$margin * 2});
    }

    @media (max-width: 767px) {
        $columns: 1;

        .posts-list__item {
            width: calc(#{100% / $columns} - #{$margin * 2});
        }
    }
}

.posts-list--related-posts {
    $margin: 12px;
    $columns: 3;

    margin: (-$margin);

    .posts-list__item {
        margin: $margin;
        width: calc(#{100% / $columns} - #{$margin * 2});
    }

    @media (min-width: 460px) and (max-width: 767px) {
        $columns: 2;

        .posts-list__item {
            width: calc(#{100% / $columns} - #{$margin * 2});
        }

        .posts-list__item:nth-child(n + 3) {
            display: none;
        }
    }

    @media (max-width: 459px) {
        $columns: 1;

        .posts-list__item {
            width: calc(#{100% / $columns} - #{$margin * 2});
        }

        .posts-list__item:nth-child(n + 3) {
            display: none;
        }
    }
}