/*
 * widget-social
 */

@import '../variables';


.widget-social__list {
    list-style: none;
    padding: 0;
    margin: -3px;
    display: flex;
}
.widget-social__item {
    margin: 3px;
    flex-basis: 0;
    flex-grow: 1;
}
.widget-social__link {
    display: block;
    padding-bottom: 100%;
    border-radius: 1.5px;
    position: relative;
    overflow: hidden;

    &, &:hover {
        color: $widget-social-color;
    }

    &:after {
        content: '';
        display: block;
        position: absolute;
        left: 0;
        right: 0;
        width: 100%;
        height: 100%;
        background-color: #000;
        z-index: 0;
        opacity: 0;
        transition: opacity .2s;
    }
}
.widget-social__link:hover:after {
    opacity: .15;
}
.widget-social__icon {
    left: 50%;
    top: 50%;
    transform: translateX(-50%) translateY(-50%);
    position: absolute;
    z-index: 1;
    font-size: 100%;
}
.widget-social__link--facebook {
    color: $widget-social-facebook-color;
    background-color: $widget-social-facebook-bg-color;
}
.widget-social__link--twitter {
    color: $widget-social-twitter-color;
    background-color: $widget-social-twitter-bg-color;
}
.widget-social__link--youtube {
    color: $widget-social-youtube-color;
    background-color: $widget-social-youtube-bg-color;
}
.widget-social__link--instagram {
    color: $widget-social-instagram-color;
    background-color: $widget-social-instagram-bg-color;
}
.widget-social__link--rss {
    color: $widget-social-rss-color;
    background-color: $widget-social-rss-bg-color;
}