/*
 * widget
 */

@import '../mixins';
@import '../variables';

.widget {
    margin-bottom: 20px;
}
.widget--card {
    @include card();

    .widget__title {
        padding: $widget-card-padding $widget-card-padding 0;
    }
    .widget__body {
        padding: $widget-card-padding;
    }
}