/*
 * block
 */

@import '../variables';


.block {
    margin-bottom: 100px;
}
.block__title {
    h2 {
        margin-bottom: 50px;
    }
}

.block--dark {
    .block__container {
        background-color: $block-dark-bg;
        padding: 60px 0;
    }
    .block__title {
        color: $block-dark-font-color;
    }
}

@media (min-width: 992px) and (max-width: 1199px) {
    .block {
        margin-bottom: 80px;
    }
    .block__title h2 {
        margin-bottom: 40px;
    }
}

@media (min-width: 768px) and (max-width: 991px) {
    .block {
        margin-bottom: 60px;
    }
    .block__title h2 {
        margin-bottom: 30px;
    }
}

@media (max-width: 767px) {
    .block {
        margin-bottom: 50px;
    }
    .block__title h2 {
        margin-bottom: 25px;
    }
}