/*
 * wishlist
 */

@import '../mixins';
@import '../variables';


.wishlist__table {
    width: 100%;
    font-size: 15px;
}
.wishlist__header {
    font-size: 14px;
    font-weight: 600;
}


.wishlist__column--image {
    a {
        width: 100%;
        display: block;
    }
    
    img {
        width: 100%;
    }
}
.wishlist__product-category {
    font-size: 13px;
    line-height: 17px;
    color: $card-font-muted-color;

    a {
        color: inherit;
        transition: color .15s;
    }
    a:hover {
        color: $card-link-hover-color;
        text-decoration: none;
    }
}
.wishlist__product-name {
    font-size: 14px;
    font-weight: 600;

    a {
        color: inherit;
        transition: color .15s;
    }
    a:hover {
        color: $card-link-hover-color;
        text-decoration: none;
    }
}
.wishlist__product-rating {
    margin-top: 6px;
}

@media (min-width: 768px) {
    .wishlist {
        @include card();
    }
    .wishlist__column {
        padding: 15px;
    }
    .wishlist__body {
        .wishlist__column {
            border-top: 1px solid $card-divider-color;
        }
    }
    .wishlist__column--image {
        text-align: center;
        width: 110px;
    }
    .wishlist__column--stock {
        text-align: center;
        width: 1px;
        white-space: nowrap;
    }
    .wishlist__column--price {
        text-align: right;
    }
    .wishlist__column--addtocart,
    .wishlist__column--remove {
        width: 1px;
        white-space: nowrap;
    }
}

@media (min-width: 768px) and (max-width: 991px) {
    .wishlist__column--info,
    .wishlist__column--addtocart,
    .wishlist__column--remove {
        padding-left: 0;
    }
}

@media (max-width: 767px) {
    .wishlist__table {
        display: block;
    }
    .wishlist__column {
        padding: 0;
    }
    .wishlist__header {
        display: none;
    }
    .wishlist__body {
        display: flex;
        flex-wrap: wrap;
        margin: -7px;

        & > tr {
            @include card();

            position: relative;
            display: block;
            margin: 7px;
            padding: 20px;
            flex-shrink: 0;
            width: calc(50% - 14px);
        }
    }
    .wishlist__column {
        display: block;
        text-align: center;
    }
    .wishlist__column--image {
    }
    .wishlist__column--info {
        margin-top: 12px;
    }
    .wishlist__column--price {
        margin-top: 12px;
        font-weight: 700;
    }
    .wishlist__column--stock {
        position: absolute;
        left: 20px;
        top: 0;

        .stock-badge {
            display: block;
        }
    }
    .wishlist__column--addtocart {
        margin-top: 16px;
    }
    .wishlist__column--remove {
        position: absolute;
        right: 0;
        top: 0;
        background-color: $card-bg-color;
        padding: 5px;
    }

    .wishlist__product-rating {
        display: flex;
        justify-content: center;
    }
}
@media (max-width: 459px) {
    .wishlist__body {
        & > tr {
            width: calc(100% - 14px);
        }
    }
}