/*
 * decor-header
 */

@import '../variables';
@import '../mixins';

.decor-header {
    @include decor-header();
}

h1.decor-header {
    @include decor-header-h1();
}
h2.decor-header {
    @include decor-header-h2();
}
h3.decor-header {
    @include decor-header-h3();
}
h4.decor-header {
    @include decor-header-h4();
}

.decor-header--align--center {
    @include decor-header-align-center();
}

.decor-header--h1 {
    @include header-h1;
}
.decor-header--h2 {
    @include header-h2;
}
.decor-header--h3 {
    @include header-h3;
}
.decor-header--h4 {
    @include header-h4;
}