/*
 * contact-us
 */

@import '../variables';

.contact-us {}
.contact-us__map {
    width: 100%;
    height: 450px;
    position: relative;

    iframe {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
    }
}
.contact-us__container {
    padding: 50px 40px 70px;
}
.contact-us__header {
    margin-bottom: 32px;
}
.contact-us__address {
    font-size: 15px;
    line-height: 1.6;
}


@media (max-width: 767px) {
    .contact-us__map {
        height: 300px;
    }
}
@media (max-width: 565px) {
    .contact-us__container {
        padding: 25px 20px 40px;
    }
}