/*
 * not-found
 */

@import '../variables';


.not-found {
    color: $body-font-alt-color;
    text-align: center;
}
.not-found__404 {
    margin-top: -30px;
    font-size: 80px;
    line-height: 80px;
    font-weight: 700;
    letter-spacing: .04em;
    margin-bottom: 20px;
}
.not-found__text {
    line-height: 26px;
    margin-bottom: 30px;
}
.not-found__search {
    width: 510px;
    margin: 0 auto 30px;
    display: flex;
}
.not-found__search-input {
    width: 0;
    flex-basis: 0;
    flex-grow: 1;
    margin-right: 8px;
}

@media (max-width: 991px) {
    .not-found__404 {
        margin-top: 0;
    }
}

@media (max-width: 575px) {
    .not-found__search {
        width: 100%;
    }
}