/*
 * footer-aboutus
 */

@import '../variables';
@import '../mixins';


.footer-aboutus {}
.footer-aboutus__title {
    margin-bottom: 28px;
}
.footer-aboutus__text {
    color: $footer-font-alt-color;
    font-size: 14px;
    line-height: 22px;
    margin-bottom: 15px;
    padding-right: 30px;
}
.footer-aboutus__contacts {
    list-style: none;
    margin: 0;
    padding: 0;
}
.footer-aboutus__contacts-item {
    font-size: 13px;
    font-weight: 600;
    padding: 4px 0;

    a {
        color: inherit;
        transition: color .15s;
    }
    a:hover {
        color: $footer-link-hover-color;
    }
    a, a:hover {
        text-decoration: none;
    }
}