/*
 * tabs
 */

@import '../variables';


.tabs--product {
    .tabs__list {
        display: flex;
        height: 70px;
        box-shadow: inset 0 -1px rgba(#000, .1);
        overflow-x: auto;
        -webkit-overflow-scrolling: touch;
    }
    .tabs__tab {
        font-size: 16px;
        font-weight: 600;
        height: 100%;
        display: flex;
        align-items: center;
        padding: 0 30px;

        &:hover {
            background-color: #f2f2f2;
            box-shadow: inset 0 -1px rgba(#000, .1);
        }
        &, &:hover {
            color: #9da3a6;
            text-decoration: none;
        }

        &:first-child {
            margin-left: auto;
        }
        &:last-child {
            margin-right: auto;
        }
    }
    .tabs__tab--active,
    .tabs__tab--active:hover {
        color: inherit;
        background-color: inherit;
        cursor: default;
        box-shadow: inset 0 -1px rgba(#000, .08), inset 0 -3px $accent-color;
    }
    .tabs__tab-content {
        overflow: hidden;
        height: 0;
        opacity: 0;
        transition: opacity .5s;
    }
    .tabs__tab-content--active {
        overflow: visible;
        height: auto;
        opacity: 1;
    }
}