/*
 * checkout
 */

@import '../variables';


.checkout__totals {
    font-size: 15px;
    line-height: 19px;
    width: 100%;
    margin-bottom: 30px;

    th, td {
        padding: 4px 0;
    }

    th {
        font-weight: 600;
    }

    tr > :first-child {
        padding-right: 20px;
    }

    tr > :last-child {
        text-align: right;
    }
}
.checkout__totals-header {
    tr:last-child > * {
        padding-bottom: 10px;
    }
}
.checkout__totals-products {
    tr:first-child > * {
        padding-top: 20px;
        border-top: 1px solid $card-divider-color;
    }
    tr:last-child > * {
        padding-bottom: 20px;
        border-bottom: 1px solid $card-divider-color;
    }
}
.checkout__totals-subtotals {
    tr:first-child > * {
        padding-top: 20px;
    }
    tr:last-child > * {
        padding-bottom: 20px;
        border-bottom: 1px solid $card-divider-color;
    }
}
.checkout__totals-footer {
    font-size: 20px;

    tr:first-child > * {
        padding-top: 20px;
    }
}



.payment-methods {
    margin-bottom: 30px;
}
.payment-methods__list {
    list-style: none;
    padding: 0;
    margin: 0;
}
.payment-methods__item {
    border-radius: 2px;
    background-color: $payment-bg-color;
    border: 1px solid $payment-border-color;

    &:hover {
        background-color: $payment-hover-bg-color;
        border-color: $payment-hover-border-color;
    }

    & + & {
        margin-top: 7px;
    }
}
.payment-methods__item-header {
    display: flex;
    align-items: center;
    padding: 9px 16px;
    font-size: 14px;
    font-weight: 600;
    cursor: pointer;
    margin-bottom: 0;
}
.payment-methods__item-radio {
    width: 14px;
    height: 14px;
    margin-right: 8px;
    position: relative;
    top: 1px;
}
.payment-methods__item-container {
    overflow: hidden;
    height: 0;
    transition: height .2s;
}
.payment-methods__item-description {
    font-size: 14px;
    padding: 0 16px 11px 16px;
}
.payment-methods__item--active {
    &, &:hover {
        border-color: $payment-active-border-color;
        background-color: $payment-active-bg-color;
    }

    .payment-methods__item-container {
        height: auto;
    }
}