/*
 * products-list
 */

.products-list--layout--grid {
    $margin: 7px;
    $columns: 4;

    display: flex;
    flex-wrap: wrap;
    margin: (-$margin);

    .products-list__item {
        flex-shrink: 0;
        margin: $margin;
        width: calc(#{100% / $columns} - #{$margin * 2});
        display: flex;
    }



    @media (min-width: 576px) and (max-width: 767px) {
        $margin: 7px;
        $columns: 3;

        margin: (-$margin);

        .products-list__item {
            margin: $margin;
            width: calc(#{100% / $columns} - #{$margin * 2});
        }
    }
    @media (max-width: 575px) and (min-width: 401px) {
        $margin: 7px;
        $columns: 2;

        margin: (-$margin);

        .products-list__item {
            margin: $margin;
            width: calc(#{100% / $columns} - #{$margin * 2});
        }
    }
    @media (max-width: 400px) {
        $margin: 7px;
        $columns: 1;

        margin: (-$margin);

        .products-list__item {
            margin: $margin;
            width: calc(#{100% / $columns} - #{$margin * 2});
        }
    }
}


