/*
 * rating
 */

@import '../variables';

.rating__body {
    display: flex;
}
.rating__star {
    & + & {
        margin-left: 3px;
    }
}
.rating__fill {
    fill: $rating-star-default-color;
}
.rating__stroke {
    fill: $rating-star-default-stroke-color;
}
.rating__star--active {
    .rating__fill {
        fill: $rating-star-active-color;
    }
    .rating__stroke {
        fill: $rating-star-active-stroke-color;
    }
}