/*
 * widget-categories
 */

@import '../variables';


.widget-categories {
    font-size: 14px;
    line-height: 18px;

    a, a:hover {
        color: inherit;
        text-decoration: none;
    }

    a:hover {
        color: $card-link-hover-color;
    }
}
.widget-categories__list {
    list-style: none;
    padding: 0;
    margin: 0 -20px;
}
.widget-categories__item {
    position: relative;

    &:before {
        left: 22px;
        top: 12px;
        display: block;
        content: '';
        position: absolute;
        width: 4px;
        height: 4px;
        background-color: $widget-categories-square-color;
    }
}
.widget-categories__arrow {
    position: relative;
    float: right;
    height: 28px;
    width: 30px;
    background-color: transparent;
    border: none;
    cursor: pointer;
    color: $widget-categories-arrow-color;
    opacity: .5;
    fill: currentColor;
    padding: 0;
    margin-right: 2px;

    &:hover {
        opacity: .8;
    }
    &:focus {
        outline: none;
    }

    svg {
        transition: transform .2s;
        position: absolute;
        left: calc(50% - 2px);
        top: calc(50% - 4px);
    }
}
.widget-categories__link {
    display: block;
    margin: 0 20px 0 35px;
    padding: 5px 0;
}
.widget-categories__counter {
    font-size: (12 / 14 * 1em);
    color: $widget-categories-counter-color;
}
.widget-categories__sublist {
    overflow: hidden;
    height: 0;
    opacity: 0;
    transition: height .2s, opacity .2s;

    .widget-categories__list {
        font-size: 14px;
        line-height: 18px;
        padding: 8px 0;
        margin: 6px 0;
        border-top: 1px solid $card-divider-color;
        border-bottom: 1px solid $card-divider-color;
    }
    .widget-categories__item {
        &:before {
            display: none;
        }
    }
    .widget-categories__link {
        margin-left: 45px;
    }
}
.widget-categories__item--opened {
    & > .widget-categories__sublist {
        height: auto;
        opacity: 1;
    }
    & > .widget-categories__arrow svg {
        transform: rotateZ(-90deg);
    }
}