/*
 * copyright
 */

@import '../variables';


.copyright {
    height: 60px;
    font-size: 13px;
    line-height: 60px;

    a {
        color: $footer-link-color;
        transition: color .15s;
    }
    a:hover {
        color: $footer-link-hover-color;
    }
    a, a:hover {
        text-decoration: none;
    }
}

@media (max-width: 575px) {
    .copyright {
        display: none;
        height: 50px;
        line-height: 50px;
        text-align: center;
    }
}