/*
 * button
 */

@import '../variables';
@import '../functions';
@import '../mixins';


.btn {
    border-radius: 1.5px;
    fill: currentColor;
    line-height: 1;
    font-weight: 600;
    transition: color .1s linear, background-color .1s linear, border-color .1s linear, box-shadow .1s linear;

    svg {
        vertical-align: middle;
    }

    /* remove border and shadow */
    &,
    &.focus,
    &:focus,
    &.focus:hover,
    &:focus:hover,
    &:not(:disabled):not(.disabled).active:focus,
    &:not(:disabled):not(.disabled):active:focus {
        border: none;
        box-shadow: none;
    }
}



/* md size - 36 (default) */
.btn {
    font-size: rem-font-size(14px);
    padding: (11 / 14 * 1em) (19 / 14 * 1em);
}
/* lg size - 42 */
.btn-lg {
    font-size: rem-font-size(15px);
    padding: (13.5 / 15 * 1em) (26 / 15 * 1em);
}
/* sm size - 28 */
.btn-sm {
    font-size: rem-font-size(13px);
    padding: (7.5 / 13 * 1em) (14 / 13 * 1em);
}
/* xs size - 24 */
.btn-xs {
    font-size: rem-font-size(12px);
    padding: (6 / 12 * 1em) (11 / 12 * 1em);
}



/* styles */
.btn-primary {
    @include button(
        $button-primary-bg,
        $button-primary-color,
        $button-primary-hover-bg,
        $button-primary-hover-color,
        $button-primary-active-bg,
        $button-primary-active-color
    );
}
.btn-secondary {
    @include button(
        $button-secondary-bg,
        $button-secondary-color,
        $button-secondary-hover-bg,
        $button-secondary-hover-color,
        $button-secondary-active-bg,
        $button-secondary-active-color
    );
}
.btn-light {
    @include button(
        $button-light-bg,
        $button-light-color,
        $button-light-hover-bg,
        $button-light-hover-color,
        $button-light-active-bg,
        $button-light-active-color
    );
}


/* svg icon */
.btn-svg-icon {
    position: relative;
    padding: 0;

    svg {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translateX(-50%) translateY(-50%);
    }
}
.btn-svg-icon {
    width: 36px;
    height: 36px;
}
.btn-svg-icon.btn-lg {
    width: 42px;
    height: 42px;
}
.btn-svg-icon.btn-sm {
    width: 28px;
    height: 28px;
}
.btn-svg-icon.btn-xs {
    width: 24px;
    height: 24px;
}



/* loading state */
@keyframes btn-loading-animation {
    from {
        transform: translateX(-50%) translateY(-50%) rotateZ(0deg);
    }
    to {
        transform: translateX(-50%) translateY(-50%) rotateZ(360deg);
    }
}

.btn-loading {
    position: relative;

    &:after {
        display: block;
        content: '';
        position: absolute;
        left: 50%;
        top: 50%;
        border: 2px solid rgba(#000, .1);
        border-top-color: rgba(#000, .5);
        border-radius: 1000px;
        transition: border-color .1s;
        transform: translateX(-50%) translateY(-50%);

        animation-name: btn-loading-animation;
        animation-duration: .5s;
        animation-timing-function: linear;
        animation-iteration-count: infinite;
    }


    /* hide button content */
    &,
    /* hover */
    &:hover,
    /* focus */
    &.focus,
    &:focus,
    /* active */
    &:not(:disabled):not(.disabled).active,
    &:not(:disabled):not(.disabled):active,
    /* disabled */
    &.disabled,
    &:disabled {
        color: rgba(#000, 0);
    }


    /* md size (default) */
    &:after {
        width: 22px;
        height: 22px;
    }
    /* lg size */
    &.btn-lg:after {
        width: 26px;
        height: 26px;
    }
    /* sm size */
    &.btn-sm:after {
        width: 16px;
        height: 16px;
    }
    /* xs size */
    &.btn-xs:after {
        width: 14px;
        height: 14px;
    }
}
