/*
 * product-card
 */

@import '../variables';
@import '../mixins';



.product-card {
    @include card();

    padding: 15px;
    position: relative;

    a, a:hover {
        text-decoration: none;
    }
}
.product-card__image {
    img {
        width: 100%;
    }
}
.product-card__badges-list {
    position: absolute;
    top: 0;
    left: 20px;
    display: flex;
}
.product-card__badge {
    font-size: 12px;
    font-weight: 600;
    text-transform: uppercase;
    padding: 4px 18px;

    & + & {
        margin-left: 6px;
    }
}
.product-card__badge--style--sale {
    background-color: $badge-sale-bg-color;
    color: $badge-sale-font-color;
}
.product-card__badge--style--new {
    background-color: $badge-new-bg-color;
    color: $badge-new-font-color;
}
.product-card__badge--style--bestseller {
    background-color: $badge-bestseller-bg-color;
    color: $badge-bestseller-font-color;
}

.product-card__actions {
    position: absolute;
    right: 0;
    top: 0;
}
.product-card__actions-list {
    display: flex;
    flex-direction: column;
    padding: 5px;
}
.product-card__category {
    font-size: 12px;
    line-height: 14px;

    a {
        color: $product-card-category-color;
        transition: color .15s;
    }
    a:hover {
        color: $product-card-category-hover-color;
    }
}
.product-card__name {
    margin-top: 5px;
    font-size: 14px;
    line-height: 16px;
    font-weight: 600;

    a {
        color: $product-card-name-color;
        transition: color .15s;
    }
    a:hover {
        color: $product-card-name-hover-color;
    }
}
.product-card__prices-list {
    opacity: 1;
    transition: opacity .2s;
}
.product-card__price {
    font-size: 15px;
    font-weight: 700;
    margin-top: 14px;
    padding-bottom: 4px;
    color: $price-default-color;
}
.product-card__price-new {
    color: $price-new-color;
}
.product-card__price-old {
    color: $price-old-color;
    font-weight: normal;
    font-size: 13px;
    text-decoration: line-through;
}




.product-card--layout--grid {
    .product-card__actions {
        overflow: hidden;
        pointer-events: none;
    }
    .product-card__actions-list {
        opacity: 0;
        transform: translateX(100%);
        transition: transform .15s, opacity .15s;
        pointer-events: auto;
    }
    .product-card__category {
        margin-top: 12px;
        text-align: center;
    }
    .product-card__name {
        text-align: center;
    }
    .product-card__rating {
        margin-top: 7px;
        display: flex;
        justify-content: center;
    }
    .product-card__rating-title {
        display: none;
    }
    .product-card__description {
        display: none;
    }
    .product-card__prices-list {
        padding: 2px 0;
    }
    .product-card__price {
        text-align: center;
    }
    .product-card__buttons {
        position: absolute;
        width: 100%;
        left: 0;
        bottom: 0;
        overflow: hidden;
    }
    .product-card__buttons-list {
        padding: 6px;
        transform: translateY(100%);
        transition: transform .2s;
    }
    .product-card__addtocart {
        display: block;
        width: 100%;
        padding-top: .785714em;
        padding-bottom: .785714em;
    }
    .product-card__compare,
    .product-card__wishlist {
        display: none;
    }
    &:hover {
        .product-card__prices-list {
            opacity: 0;
        }
        .product-card__buttons-list {
            transform: translateY(0);
        }
        .product-card__actions-list {
            transform: translateX(0);
            opacity: 1;
        }
    }
}






.product-card--layout--list {
    display: flex;
    position: relative;

    .product-card__image {
        flex-shrink: 0;
        width: 225px;
        margin-right: 20px;
    }
    .product-card__name {
        font-size: 15px;
    }
    .product-card__description {
        margin-top: 16px;
        font-size: 13px;
        line-height: 20px;
        color: $card-font-alt-muted-color;
    }
    .product-card__rating {
        display: flex;
        font-size: 12px;
        line-height: 14px;
        color: $product-card-category-color;
    }
    .product-card__price {
        margin-top: 14px;
        font-size: 16px;
    }
    .product-card__buttons {
        margin-top: 22px;
    }
    .product-card__buttons-list {
        display: flex;
    }
    .product-card__wishlist {
        margin-left: 8px;
        margin-right: 2px;
    }
    .product-card__actions {
        display: none;
    }

    @media (min-width: 768px) {
        .product-card__category {
            margin-top: 6px;
        }
        .product-card__rating {
            position: absolute;
            top: 22px;
            right: 22px;
        }
        .product-card__rating-title {
            margin-right: 10px;
        }
    }
    @media (max-width: 767px) {
        .product-card__image {
            margin-right: 15px;
            width: 125px;
        }
        .product-card__rating {
            margin-top: 10px;
        }
        .product-card__rating-title {
            margin-left: 10px;
            order: 1;
        }
        .product-card__description {
            margin-top: 12px;
            max-height: 40px;
            overflow: hidden;
            text-overflow: ellipsis;
        }
        .product-card__price {
            margin-top: 12px;
        }
        .product-card__buttons {
            margin-top: 16px;
        }
    }
    @media (max-width: 459px) {
        display: block;
        padding: 25px;

        .product-card__image {
            margin-right: 0;
            margin-bottom: 20px;
            width: auto;
        }
    }
}