/*
 * footer-followus
 */

@import '../variables';
@import '../mixins';


.footer-followus {

}
.footer-followus__title {
    margin-bottom: 28px;
}
.footer-followus__header {
    @include decor-header();
}
.footer-followus__text {
    color: $footer-font-alt-color;
    font-size: 14px;
    line-height: 22px;
    margin-bottom: 15px;
}
.footer-followus__subscribe-form {
    display: flex;
    margin-bottom: 15px;

    .btn {
        margin-left: 6px;
    }
}
.footer-followus__social-links {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
}
.footer-followus__social-link a {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 28px;
    height: 28px;
    margin-right: 5px;
    font-size: 16px;
    border-radius: 1.5px;
    background: $footer-social-icon-bg;
    color: $footer-social-icon-color;

    &:hover {
        color: $footer-social-icon-hover-color;
        background: $footer-social-icon-hover-bg;
        text-decoration: none;
    }
}

@media (min-width: 768px) {
    .footer-followus__header {
        @include decor-header-h4();
    }
}
@media (max-width: 767px) {
    .footer-followus {
        text-align: center;
        padding: 35px 10px 30px;
    }
    .footer-followus__title {
        margin-bottom: 25px;
    }
    .footer-followus__header {
        @include decor-header-align-center();
        @include decor-header-h2();
    }
    .footer-followus__text {
        margin-bottom: 18px;
    }
    .footer-followus__subscribe-form {
        margin-bottom: 25px;
    }
    .footer-followus__social-links {
        justify-content: center;
    }
}