/*
 * search
 */

@import '../variables';

.search {
    position: absolute;
    height: 100%;
    width: 100%;
    background: $search-bg;
    color: $search-font-color;
    z-index: 100;
    box-shadow: $search-shadow;
    opacity: 0;
    transform-origin: top center;
    transform: translateY(-100%);
    transition: opacity .2s, transform .2s;
}
.search--opened {
    opacity: 1;
    transform: translateY(0);
}

.search__form {
    display: flex;
    height: 100%;
}
.search__input {
    width: 1px;
    flex-basis: 0;
    background-color: transparent;
    border: none;
    padding: 0 30px 4px;
    margin: 0;
    flex-grow: 1;
    font-family: inherit;
    font-size: 22px;
    letter-spacing: -.02em;
    font-weight: 600;
    color: inherit;

    &:focus {
        outline: none;
    }

    &::placeholder {
        color: $search-placeholder-color;
    }
}
.search__button {
    flex-shrink: 0;
    background-color: transparent;
    border: none;
    padding: 0;
    margin: 0;
    width: 70px;
    color: $search-button-color;
    position: relative;
    cursor: pointer;
    transition: color .2s;

    svg {
        position: absolute;
        left: calc(50% - 10px);
        top: calc(50% - 10px);
        fill: currentColor;
    }

    &:focus {
        outline: none;
    }

    &:hover {
        color: $search-button-hover-color;
    }

    & + & {
        border-left: 1px solid $search-divider-color;
    }
}

@media (max-width: 575px) {
    .search__input {
        font-size: 18px;
        padding: 0 18px 2px;
    }
    .search__button {
        width: 54px;
    }
}