/*
 * footer
 */

@import '../variables';


.footer {
    background-color: $footer-bg-color;
    color: $footer-font-color;
}
.footer__payments {
    display: flex;
    align-items: center;
}


@media (min-width: 576px) {
    .footer__widgets {
        border-top: $footer-border-top;
        border-bottom: $footer-border-bottom;
        padding-bottom: 50px;
        padding-top: 50px;
    }
}
@media (min-width: 768px) and (max-width: 991px) {
    .footer__followus {
        margin-top: 40px;
    }
}
@media (max-width: 767px) {
    .footer__aboutus {
        display: none;
    }
}
@media (max-width: 575px) {
    .footer__widgets {
        padding-top: 30px;
    }
    .footer__followus {
        margin-top: 5px;
        border-top: $footer-border-top;
        border-bottom: $footer-border-bottom;
    }
    .footer__payments {
        //display: none;
        height: 50px;
        line-height: 50px;
        text-align: center;
    }
}