/*
 * pagination
 */

@import '../variables';


.pagination {
    margin: 0;
    font-size: 15px;
    font-weight: 600;
    text-align: center;
}
.page-link {
    color: $pagination-link-color;
    fill: currentColor;
    border-radius: 18px;
    border: 2px solid transparent;
    background-color: transparent;
    margin: 0;
    padding: 8px 10px;
    min-width: 36px;
    height: 36px;
    line-height: 1;
}
.page-item + .page-item {
    margin-left: 4px;
}
.page-item.active,
.page-item.active + .page-item {
    margin-left: 8px;
}
.page-item:first-child .page-link {
    border-radius: 18px;
}
.page-item:last-child .page-link {
    border-radius: 18px;
}
.page-link:hover {
    color: $pagination-link-hover-color;
    background-color: $pagination-link-hover-bg-color;
    border-color: transparent;
}
.page-item.active .page-link {
    color: $pagination-link-active-color;
    background-color: transparent;
    border-color: $pagination-link-active-border-color;
    cursor: default;
}
.page-item.disabled .page-link {
    color: $pagination-link-disabled-color;
    background-color: transparent;
    border-color: transparent;
}
.page-item.active .page-link:focus,
.page-link:focus {
    background-color: $pagination-link-hover-bg-color;
    box-shadow: none;
}