/*
 * menu
 */

@import '../variables';


.menu {
    box-shadow: $menu-shadow;
    background: $menu-bg;
    color: $menu-font-color;
    list-style: none;
    margin: 0;
    padding: 12px 0;
    width: 190px;
}
.menu__item {
    position: relative;
}
.menu__sub {
    position: absolute;
    left: 100%;
    top: -12px;
    visibility: hidden;
    transform-origin: left;
    transform: rotateY(45deg);
    opacity: 0;
    transition: transform .2s, opacity .2s;
}
.menu__link {
    display: flex;
    justify-content: space-between;
    padding: 7px 20px;
    font-size: 13px;
    line-height: 15px;
    font-weight: 600;

    &, &:hover {
        color: inherit;
        text-decoration: none;
    }
}
.menu__link-arrow {
    flex-shrink: 0;
    align-self: center;
    fill: $menu-arrow-color;
    margin-right: -2px;
    margin-bottom: 1px;
    margin-left: 10px;
}
.menu__item:hover > .menu__link {
    background-color: $menu-item-hover-bg-color;
}

.menu__item:hover {
    & > .menu__sub {
        opacity: 1;
        visibility: visible;
        transform: rotateY(0deg);
    }
}