/*
 * comparison
 */

@import '../mixins';
@import '../variables';


.comparison {
    @include card();

    font-size: 15px;
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;

    table {
        border-spacing: 0;
        width: 100%;
    }
    th, td {
        border-top: 1px solid $card-divider-color;
        padding: 15px 20px;
    }

    th {
        font-size: 14px;
        font-weight: 600;
        background-color: $comparison-th-bg;
        text-align: right;
        width: 200px;
    }
    td {
        border-left: 1px solid $card-divider-color;
        text-align: center;
    }

    tr:first-child th,
    tr:first-child td {
        border-top: none;
    }
}
.comparison__product-link {
    & {
        color: inherit;
        transition: color .15s;
    }
    &:hover {
        color: $card-link-hover-color;
        text-decoration: none;
    }
}
.comparison__product-image {
    margin-bottom: 8px;

    img {
        vertical-align: middle;
        max-width: 180px;
    }
}
.comparison__product-name {
    font-weight: 600;
}
.comparison__product-rating {
    margin-top: 6px;
    display: flex;
    justify-content: center;
}