/*
 * block-teammates
 */

@import '../variables';


.block-teammates__list {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin-right: -15px;
    margin-bottom: -15px;
}
.block-teammates__item {
    flex-shrink: 0;
    display: flex;
    width: 210px;
    margin-right: 15px;
    margin-bottom: 15px;
}
.block-teammates__image {
    max-width: 100%;
}
.block-teammates__info {
    padding: 10px 15px;
    text-align: center;
}
.block-teammates__name {
    font-size: 15px;
    line-height: 18px;
    font-weight: 600;
    padding: 2px 0;
}
.block-teammates__position {
    font-size: 13px;
    line-height: 15px;
    color: $card-font-alt-muted-color;
    padding: 2px 0;
}
.block-teammates__social {
    list-style: none;
    padding: 0;
    margin: 10px 0 8px;
    display: flex;
    justify-content: center;

    .block-teammates__social li {
        margin: 4px;
    }
}
.block-teammates__social-link {
    color: $card-font-alt-color;
    transition: color .15s;
    margin: 0 8px;
    font-size: 14px;
    
    &:hover {
        color: $card-link-hover-color;
        text-decoration: none;
    }
}