/*
 * widget-tags
 */

@import '../variables';


.widget-tags {
    display: flex;
    flex-wrap: wrap;
    font-size: 12px;
    font-weight: 600;
    line-height: 15px;
    margin: -3px;

    a {
        display: block;
        border: 1px solid $widget-tags-border-color;
        border-radius: 2px;
        margin: 3px;
        padding: 5px 9px;
        transition: background-color .15s, border-color .15s;
    }

    a, a:hover {
        color: inherit;
        text-decoration: none;
    }

    a:hover {
        color: $widget-tags-hover-color;
        background-color: $widget-tags-hover-bg-color;
        border-color: $widget-tags-hover-bg-color;
    }
}