@import 'custom-variables';



// -------------------------
// --- colors
// -------------------------
    $dark-color:                     #2e3133               !default;
    $dark-opposite-color:            #fff                  !default;
          
    $light-color:                    #fff                  !default;
    $light-opposite-color:           #333                  !default;
    $light-opposite-muted-color:     #999                  !default;
    $light-opposite-alt-color:       #505659               !default;
    $light-opposite-alt-muted-color: #7e888c               !default;
    $light-divider-color:            #ebebeb               !default;
          
    $theme-color:                    #e6eef2               !default;
    $theme-opposite-color:           $light-opposite-color !default;
    $theme-opposite-alt-color:       #676f73               !default;
    $theme-divider-color:            #cfdee6               !default;
          
    $accent-color:                   #ffd599               !default;
    $accent-opposite-color:          $light-opposite-color !default;



// -------------------------
// --- base
// -------------------------
    $base-font-family: 'Open Sans', sans-serif !default;
    $base-font-size:   16px                    !default;
    $base-font-weight: 400                     !default;
    $base-line-height: 1.5                     !default;



// -------------------------
// --- body
// -------------------------
    $body-bg:               $theme-color              !default;
    $body-font-color:       $theme-opposite-color     !default;
    $body-font-alt-color:   $theme-opposite-alt-color !default;
    $body-link-color:       #f28955                   !default;
    $body-link-hover-color: #f26924                   !default;



// -------------------------
// --- header
// -------------------------
    $header-bg:                     transparent                                          !default;
    $header-font-color:             inherit                                              !default;
    $header-border-color:           $theme-divider-color                                 !default;
    $header-border-bottom:          1px solid $header-border-color                       !default;
    
    // navigation
    $header-arrow-color:            rgba(#000, .3)                                       !default;
    $header-menu-icon-color:        #a1adb2                                              !default;
    $header-menu-hover-icon-color:  $header-menu-icon-color                              !default;
    $header-menu-focus-icon-color:  $header-menu-hover-icon-color                        !default;
    $header-item-hover-bg-color:    rgba(#000, .04)                                      !default;
    $header-item-focus-bg-color:    rgba(#000, .04)                                      !default;
    $header-item-hover-arrow-color: $header-arrow-color                                  !default;
    $header-dropdown-shadow:        0 1px 4px rgba(#000, .1), 0 2px 15px rgba(#000, .15) !default;
    
    // menu
    $menu-bg:                       $light-color                                         !default;
    $menu-font-color:               $light-opposite-color                                !default;
    $menu-arrow-color:              rgba(#000, .3)                                       !default;
    $menu-item-hover-bg-color:      #e6eef2                                              !default;
    $menu-shadow:                   $header-dropdown-shadow                              !default;
    
    // search
    $search-bg:                     $light-color                                         !default;
    $search-font-color:             $light-opposite-color                                !default;
    $search-shadow:                 $header-dropdown-shadow                              !default;
    $search-placeholder-color:      #b2b2b2                                              !default;
    $search-button-color:           #b2b2b2                                              !default;
    $search-button-hover-color:     #4d4d4d                                              !default;
    $search-divider-color:          $light-divider-color                                 !default;
    
    // megamenu
    $megamenu-bg:                   $light-color                                         !default;
    $megamenu-font-color:           $light-opposite-color                                !default;
    $megamenu-link-hover-color:     $body-link-hover-color                               !default;
    $megamenu-shadow:               $header-dropdown-shadow                              !default;
    
    // dropcart
    $dropcart-bg:                   $light-color                                         !default;
    $dropcart-font-color:           $light-opposite-color                                !default;
    $dropcart-font-alt-color:       $light-opposite-alt-muted-color                      !default;
    $dropcart-shadow:               $header-dropdown-shadow                              !default;
    $dropcart-divider-color:        $light-divider-color                                 !default;



// -------------------------
// --- mobilemenu
// -------------------------
    $mobilemenu-backdrop-bg:            #333                            !default;
    $mobilemenu-bg:                     $light-color                    !default;
    $mobilemenu-font-color:             $light-opposite-color           !default;
    $mobilemenu-font-alt-muted-color:   $light-opposite-alt-muted-color !default;
    $mobilemenu-divider-color:          $light-divider-color            !default;
    $mobilemenu-close-icon-color:       #b3b3b3                         !default;
    $mobilemenu-close-icon-hover-color: #4d4d4d                         !default;
    $mobilemenu-item-hover-bg-color:    #f2f2f2                         !default;
    $mobilemenu-arrow-color:            #b3b3b3                         !default;   



// -------------------------
// --- card
// -------------------------
    $card-bg-color:             $light-color                                          !default;
    $card-font-color:           $light-opposite-color                                 !default;
    $card-font-muted-color:     $light-opposite-muted-color                           !default;
    $card-font-alt-color:       $light-opposite-alt-color                             !default;
    $card-font-alt-muted-color: $light-opposite-alt-muted-color                       !default;
    $card-shadow:               0 0 0 1px rgba(#000, .02), 0 1px 4px rgba(#cfdee5, 1) !default;
    $card-divider-color:        $light-divider-color                                  !default;
    $card-link-color:           $body-link-color                                      !default;
    $card-link-hover-color:     $body-link-hover-color                                !default;



// -------------------------
// --- details - also known as collapse, spoiler, etc.
// -------------------------
    $details-shadow:         $card-shadow        !default;
    $details-bg-color:       $card-bg-color      !default;
    $details-font-color:     $card-font-color    !default;
    $details-arrow-color:    #999                !default;
    $details-hover-bg-color: #f2f7fa             !default;
    $details-divider-color:  $card-divider-color !default;



// -------------------------
// --- footer
// -------------------------
    $footer-bg-color:                transparent                    !default;
    $footer-font-color:              $body-font-color               !default;
    $footer-font-alt-color:          $body-font-alt-color           !default;
    $footer-border-top:              1px solid $theme-divider-color !default;
    $footer-border-bottom:           1px solid $theme-divider-color !default;
    $footer-link-color:              $footer-font-alt-color         !default;
    $footer-link-hover-color:        $body-link-hover-color         !default;
    // social icons
    $footer-social-icon-color:       $footer-font-alt-color         !default;
    $footer-social-icon-bg:          transparent                    !default;
    $footer-social-icon-hover-color: $footer-social-icon-color      !default;
    $footer-social-icon-hover-bg:    #cfdee5                        !default;
    
    // if screen width less than md (<768px)
    // --- details - also known as collapse, spoiler, etc.
    $footer-details-shadow:          $details-shadow                !default;
    $footer-details-bg-color:        $details-bg-color              !default;
    $footer-details-font-color:      $details-font-color            !default;
    $footer-details-arrow-color:     $details-arrow-color           !default;
    $footer-details-hover-bg-color:  $details-hover-bg-color        !default;
    $footer-details-divider-color:   $details-divider-color         !default;
    // --- links widget
    $footer-links-border-radius:     0                              !default;
    $footer-links-font-color:        $footer-font-alt-color         !default;
    $footer-links-hover-font-color:  $footer-font-alt-color         !default;
    $footer-links-hover-bg-color:    #e6eef2                        !default;



// -------------------------
// --- decor header (decorated title)
// -------------------------
    $decor-header-underline-color: $accent-color !default;



// -------------------------
// --- product
// -------------------------
    // rating
    $rating-star-default-color:         #dae2e5                      !default;
    $rating-star-default-stroke-color:  rgba(#000, .07)              !default;
    $rating-star-active-color:          #ffd333                      !default;
    $rating-star-active-stroke-color:   rgba(#000, .07)              !default;
    
    // badges
    $badge-sale-bg-color:               #ff5959                      !default;
    $badge-sale-font-color:             #fff                         !default;
    $badge-new-bg-color:                #5990ff                      !default;
    $badge-new-font-color:              #fff                         !default;
    $badge-bestseller-bg-color:         #9f59ff                      !default;
    $badge-bestseller-font-color:       #fff                         !default;
    
    // stock badges
    $badge-stock-bg:                    #a5d28f                      !default;
    $badge-stock-color:                 #333                         !default;
    
    // prices
    $price-default-color:               $light-opposite-color        !default;
    $price-new-color:                   #ff4040                      !default;
    $price-old-color:                   #a6a6a6                      !default;
    
    // card
    $product-card-category-color:       #b2b2b2                      !default;
    $product-card-category-hover-color: $card-link-hover-color       !default;
    $product-card-name-color:           inherit                      !default;
    $product-card-name-hover-color:     $card-link-hover-color       !default;



// -------------------------
// --- breadcrumbs
// -------------------------
    $breadcrumb-link-color:        #8a9499                !default;
    $breadcrumb-link-hover-color:  $body-link-hover-color !default;
    $breadcrumb-link-active-color: $breadcrumb-link-color !default;
    $breadcrumb-divider-color:     $breadcrumb-link-color !default;



// -------------------------
// --- alerts
// -------------------------
    $alert-close-button-color:       #b3b3b3 !default;
    $alert-close-button-hover-color: #737373 !default;
    
    $alert-primary-color:            #7eb2e6 !default;
    $alert-primary-font-color:       #4274a6 !default;
    
    $alert-success-color:            #87bf7c !default;
    $alert-success-font-color:       #359a21 !default;
    
    $alert-danger-color:             #e67e7e !default;
    $alert-danger-font-color:        #bf2525 !default;
    
    $alert-warning-color:            #e5b85c !default;
    $alert-warning-font-color:       #cd8e0e !default;



// -------------------------
// --- pagination
// -------------------------
    $pagination-link-color:               $body-font-color       !default;
    $pagination-link-hover-color:         $pagination-link-color !default;
    $pagination-link-hover-bg-color:      #cfdee5                !default;
    $pagination-link-active-color:        $pagination-link-color !default;
    $pagination-link-active-border-color: #acb9bf                !default;
    $pagination-link-disabled-color:      #95a0a6                !default;



// -------------------------
// --- slider
// -------------------------
    // dot
    $slider-dot-color:                   #c3d2d9                          !default;
    $slider-dot-hover-color:             darken($slider-dot-color, 7%)    !default;
    $slider-dot-active-color:            $accent-color                    !default;
    // dot - dark
    $slider-dot-dark-color:              #495054                          !default;
    $slider-dot-dark-hover-color:        #535a5e    !default;
    $slider-dot-dark-active-color:       $accent-color                    !default;
    // button
    $slider-button-bg-color:             #cfdee5                          !default;
    $slider-button-arrow-color:          #505659                          !default;
    // button hover
    $slider-button-hover-bg-color:       #c3d2d9                          !default;
    $slider-button-hover-arrow-color:    $slider-button-arrow-color       !default;
    // button active
    $slider-button-active-bg-color:      $slider-button-hover-bg-color    !default;
    $slider-button-active-arrow-color:   $slider-button-hover-arrow-color !default;
    // button disabled
    $slider-button-disabled-bg-color:    #e6eef2                          !default;
    $slider-button-disabled-arrow-color: rgba(#505659, .2)                !default;



// -------------------------
// --- nouislider
// -------------------------
    $nouislider-bg-color:      #e5e5e5       !default;
    $nouislider-connect-color: $accent-color !default;
    $nouislider-handle-color:  $accent-color !default;
    $nouislider-border-color:  #505659       !default;



// -------------------------
// --- buttons
// -------------------------
    // primary
    $button-primary-bg:             $accent-color          !default;
    $button-primary-color:          $accent-opposite-color !default;
    $button-primary-hover-bg:       #576166                !default;
    $button-primary-hover-color:    #fff                   !default;
    $button-primary-active-bg:      #363d40                !default;
    $button-primary-active-color:   #fff                   !default;
    // secondary
    $button-secondary-bg:           #cfdee5                !default;
    $button-secondary-color:        #4d4d4d                !default;
    $button-secondary-hover-bg:     #576166                !default;
    $button-secondary-hover-color:  #fff                   !default;
    $button-secondary-active-bg:    #363d40                !default;
    $button-secondary-active-color: #fff                   !default;
    // light, this style is used in the product card for 'quick view', 'wishlist', 'compare' buttons
    $button-light-bg:               transparent            !default;
    $button-light-color:            #ccc                   !default;
    $button-light-hover-bg:         #e6eef2                !default;
    $button-light-hover-color:      #4d4d4d                !default;
    $button-light-active-bg:        #cfdee5                !default;
    $button-light-active-color:     #4d4d4d                !default;
    // remove, this style is used in 'dropcart', 'cart', 'wishlist' and looks like a cross
    $button-remove-color:           #b3b3b3                !default;
    $button-remove-hover-color:     #ff4040                !default;
    // layout, this style is used for buttons that switch the shop layout
    $layout-button-color:           #95a0a6                !default;
    $layout-button-hover-color:     $layout-button-color   !default;
    $layout-button-hover-bg-color:  #f2f2f2                !default;
    $layout-button-active-color:    #505659                !default;



// -------------------------
// --- forms
// -------------------------
    $valid-feedback-color:      #359a21                   !default;
    $invalid-feedback-color:    #bf2525                   !default;
    
    // form control
    // --- normal
    $form-control-bg:           #fff                      !default;
    $form-control-color:        inherit                   !default;
    $form-control-border:       1px solid rgba(#000, .15) !default;
    $form-control-shadow:       none                      !default;
    // --- hover
    $form-control-hover-bg:     $form-control-bg          !default;
    $form-control-hover-color:  $form-control-color       !default;
    $form-control-hover-border: 1px solid rgba(#000, .25) !default;
    $form-control-hover-shadow: none                      !default;
    // --- focus
    $form-control-focus-bg:     $form-control-bg          !default;
    $form-control-focus-color:  $form-control-color       !default;
    $form-control-focus-border: 1px solid #8fb8cc         !default;
    $form-control-focus-shadow: 0 0 6px rgba(#8fb8cc, .7) !default;
    
    // body form control
    // --- normal
    $body-form-control-bg:           #cfdee5                                       !default;
    $body-form-control-color:        inherit                                       !default;
    $body-form-control-border:       1px solid $body-form-control-bg               !default;
    $body-form-control-shadow:       none                                          !default;
    // --- hover
    $body-form-control-hover-bg:     #c8d7de                                       !default;
    $body-form-control-hover-color:  inherit                                       !default;
    $body-form-control-hover-border: 1px solid $body-form-control-hover-bg         !default;
    $body-form-control-hover-shadow: none                                          !default;
    // --- focus
    $body-form-control-focus-bg:     $form-control-bg                              !default;
    $body-form-control-focus-color:  $form-control-color                           !default;
    $body-form-control-focus-border: 1px solid $body-form-control-hover-bg         !default;
    $body-form-control-focus-shadow: 0 0 6px rgba($body-form-control-hover-bg, .7) !default;
    
    // footer form control
    // --- normal
    $footer-form-control-bg:           $body-form-control-bg           !default;
    $footer-form-control-color:        $body-form-control-color        !default;
    $footer-form-control-border:       $body-form-control-border       !default;
    $footer-form-control-shadow:       $body-form-control-shadow       !default;
    // --- hover
    $footer-form-control-hover-bg:     $body-form-control-hover-bg     !default;
    $footer-form-control-hover-color:  $body-form-control-hover-color  !default;
    $footer-form-control-hover-border: $body-form-control-hover-border !default;
    $footer-form-control-hover-shadow: $body-form-control-hover-shadow !default;
    // --- focus
    $footer-form-control-focus-bg:     $body-form-control-focus-bg     !default;
    $footer-form-control-focus-color:  $body-form-control-focus-color  !default;
    $footer-form-control-focus-border: $body-form-control-focus-border !default;
    $footer-form-control-focus-shadow: $body-form-control-focus-shadow !default;
    
    // valid form control
    // --- normal
    $form-control-valid-bg:           $form-control-bg          !default;
    $form-control-valid-color:        $form-control-color       !default;
    $form-control-valid-border:       1px solid #87bf7c         !default;
    $form-control-valid-shadow:       none                      !default;
    // --- hover
    $form-control-valid-hover-bg:     $form-control-valid-bg    !default;
    $form-control-valid-hover-color:  $form-control-valid-color !default;
    $form-control-valid-hover-border: 1px solid #68a65b         !default;
    $form-control-valid-hover-shadow: none                      !default;
    // --- focus
    $form-control-valid-focus-bg:     $form-control-valid-bg    !default;
    $form-control-valid-focus-color:  $form-control-valid-color !default;
    $form-control-valid-focus-border: 1px solid #68a65b         !default;
    $form-control-valid-focus-shadow: 0 0 6px rgba(#68a65b, .7) !default;
    
    // invalid form control
    // --- normal
    $form-control-invalid-bg:           $form-control-bg            !default;
    $form-control-invalid-color:        $form-control-color         !default;
    $form-control-invalid-border:       1px solid #e67e7e           !default;
    $form-control-invalid-shadow:       none                        !default;
    // --- hover
    $form-control-invalid-hover-bg:     $form-control-invalid-bg    !default;
    $form-control-invalid-hover-color:  $form-control-invalid-color !default;
    $form-control-invalid-hover-border: 1px solid #cc5c5c           !default;
    $form-control-invalid-hover-shadow: none                        !default;
    // --- focus
    $form-control-invalid-focus-bg:     $form-control-invalid-bg    !default;
    $form-control-invalid-focus-color:  $form-control-invalid-color !default;
    $form-control-invalid-focus-border: 1px solid #cc5c5c           !default;
    $form-control-invalid-focus-shadow: 0 0 6px rgba(#cc5c5c, .7)   !default;
    
    // form radio select - this style is used in the product page, see the 'material' field
    // --- normal
    $radio-select-font-color:            $light-opposite-color    !default;
    $radio-select-bg-color:              $light-color             !default;
    $radio-select-border-color:          rgba(0, 0, 0, .15)       !default;
    // --- hover
    $radio-select-hover-font-color:      $radio-select-font-color !default;
    $radio-select-hover-bg-color:        #f2f2f2                  !default;
    $radio-select-hover-border-color:    rgba(0, 0, 0, .1)        !default;
    // --- active
    $radio-select-active-font-color:     $accent-opposite-color   !default;
    $radio-select-active-bg-color:       $accent-color            !default;
    $radio-select-active-border-color:   rgba(0, 0, 0, .08)       !default;
    // --- disabled
    $radio-select-disabled-font-color:   #999                     !default;
    $radio-select-disabled-bg-color:     #f5f5f5                  !default;
    $radio-select-disabled-border-color: rgba(0, 0, 0, .08)       !default;



// -------------------------
// --- payments - list of payment methods on the checkout page
// -------------------------
    // normal
    $payment-bg-color:            #fff            !default;
    $payment-border-color:        rgba(#000, .1)  !default;
    // hover
    $payment-hover-bg-color:      #f7f7f7         !default;
    $payment-hover-border-color:  rgba(#000, .1)  !default;
    // active
    $payment-active-bg-color:     #ebf3f7         !default;
    $payment-active-border-color: rgba(#000, .06) !default;



// -------------------------
// --- widget
// -------------------------
    $widget-card-padding: 20px !default;



// -------------------------
// --- widget social
// -------------------------
    $widget-social-color:              #fff                 !default;
    // facebook
    $widget-social-facebook-color:     $widget-social-color !default;
    $widget-social-facebook-bg-color:  #3c5a99              !default;
    // twitter
    $widget-social-twitter-color:      $widget-social-color !default;
    $widget-social-twitter-bg-color:   #00a2e8              !default;
    // youtube
    $widget-social-youtube-color:      $widget-social-color !default;
    $widget-social-youtube-bg-color:   #e52e2e              !default;
    // instagram
    $widget-social-instagram-color:    $widget-social-color !default;
    $widget-social-instagram-bg-color: #815dc7              !default;
    // rss
    $widget-social-rss-color:          $widget-social-color !default;
    $widget-social-rss-bg-color:       #ffc338              !default;



// -------------------------
// --- widget tags
// -------------------------
    $widget-tags-border-color:   #d9d9d9                !default;
    $widget-tags-hover-bg-color: $accent-color          !default;
    $widget-tags-hover-color:    $accent-opposite-color !default;



// -------------------------
// --- widget categories
// -------------------------
    $widget-categories-square-color:  #505659 !default;
    $widget-categories-arrow-color:   #000    !default;
    $widget-categories-counter-color: #b3b3b3 !default;



// -------------------------
// --- widget filters
// -------------------------
    $filter-price-title-color: #999    !default;
    $filter-counter-color:     #a6a6a6 !default;



// -------------------------
// --- block
// -------------------------
    $block-dark-bg:         $dark-color          !default;
    $block-dark-font-color: $dark-opposite-color !default;



// -------------------------
// --- block gallery
// -------------------------
    $block-gallery-card-bg-color:       rgba(51, 51, 51, .97) !default;
    $block-gallery-card-font-color:     #fff                  !default;
    $block-gallery-card-font-alt-color: #a1adb2               !default;



// -------------------------
// --- block collections
// -------------------------
    $block-collections-description-color: #8a9499        !default;
    $block-collections-divider-color:     rgba(#000, .1) !default;



// -------------------------
// --- block testimonials
// -------------------------
    $testimonial-icon-color:          #676f73 !default;
    $testimonial-dark-text-color:     #fff    !default;
    $testimonial-dark-text-alt-color: #a1adb2 !default;



// -------------------------
// --- product comparison
// -------------------------
    $comparison-th-bg: #f7f7f7 !default;



// -------------------------
// --- typography
// -------------------------
    $headers-font-family: inherit              !default;
    
    $h1-font-family:      $headers-font-family !default;
    $h1-font-size:        36px                 !default;
    $h1-font-weight:      700                  !default;
    $h1-font-style:       normal               !default;
    $h1-letter-spacing:   -.04em               !default;
    $h1-line-height:      1.2                  !default;
    $h1-text-transform:   none                 !default;
    
    $h2-font-family:      $headers-font-family !default;
    $h2-font-size:        30px                 !default;
    $h2-font-weight:      700                  !default;
    $h2-font-style:       normal               !default;
    $h2-letter-spacing:   -.04em               !default;
    $h2-line-height:      1.2                  !default;
    $h2-text-transform:   none                 !default;
    
    $h3-font-family:      $headers-font-family !default;
    $h3-font-size:        24px                 !default;
    $h3-font-weight:      700                  !default;
    $h3-font-style:       normal               !default;
    $h3-letter-spacing:   -.04em               !default;
    $h3-line-height:      1.2                  !default;
    $h3-text-transform:   none                 !default;
    
    $h4-font-family:      $headers-font-family !default;
    $h4-font-size:        20px                 !default;
