/*
 * product-grid-card
 */

@import '../variables';

$margin: 5px;
$columns: 4;

.products-grid-list .card{
	//background-color: $theme-color!important;
}

.products-grid-list {
    margin: (-$margin);
    display: flex;
    flex-wrap: wrap;
    backface-visibility: hidden;
}

.product-grid-card {
    margin: $margin;
    width: calc(#{100% / $columns} - #{$margin * 2});
    padding: 0px;
    text-align: center;

    a {
        display: block;
        transition: color .15s;
    }
    a, a:hover {
        color: inherit;
        text-decoration: none;
    }
    a:hover {
        color: $card-link-hover-color;
    }
}

.product-grid-footer{
	padding:15px;
}
.product-grid-card__image {
    img {
        width: 100%;
    }
}
.product-grid-card__name {
    margin-top: 14px;
    font-size: 15px;
    line-height: 17px;
    font-weight: 600;
    letter-spacing: -.02em;
}
.product-grid-card__products {
    font-size: 13px;
    color: $card-font-muted-color;
    line-height: 15px;
    margin-top: 3px;
}

.product-grid-card__addtocart{
	margin-top: 1em;
    display: block;
    width: 100%;
    padding-top: .785714em;
    padding-bottom: .785714em;
 }

@media (min-width: 768px) and (max-width: 991px) {
    $columns: 4;

    .product-grid-card {
        width: calc(#{100% / $columns} - #{$margin * 2});
    }
}
@media (min-width: 520px) and (max-width: 767px) {
    $columns: 3;

    .product-grid-card {
        width: calc(#{100% / $columns} - #{$margin * 2});
    }
}
@media (max-width: 519px) {
    $columns: 2;

    .product-grid-card {
        width: calc(#{100% / $columns} - #{$margin * 2});
    }
}