/*
 * category-card
 */

@import '../variables';

$margin: 5px;
$columns: 6;

.categories-list {
    margin: (-$margin);
    display: flex;
    flex-wrap: wrap;
    backface-visibility: hidden;
}

.category-card {
    margin: $margin;
    width: calc(#{100% / $columns} - #{$margin * 2});
    padding: 15px;
    text-align: center;

    a {
        display: block;
        transition: color .15s;
    }
    a, a:hover {
        color: inherit;
        text-decoration: none;
    }
    a:hover {
        color: $card-link-hover-color;
    }
}
.category-card__image {
    img {
        width: 100%;
    }
}
.category-card__name {
    margin-top: 14px;
    font-size: 15px;
    line-height: 17px;
    font-weight: 600;
    letter-spacing: -.02em;
}
.category-card__products {
    font-size: 13px;
    color: $card-font-muted-color;
    line-height: 15px;
    margin-top: 3px;
}

@media (min-width: 768px) and (max-width: 991px) {
    $columns: 4;

    .category-card {
        width: calc(#{100% / $columns} - #{$margin * 2});
    }
}
@media (min-width: 520px) and (max-width: 767px) {
    $columns: 3;

    .category-card {
        width: calc(#{100% / $columns} - #{$margin * 2});
    }
}
@media (max-width: 519px) {
    $columns: 2;

    .category-card {
        width: calc(#{100% / $columns} - #{$margin * 2});
    }
}