/*
 * radio-color
 */

.radio-color {
    user-select: none;
}
.radio-color__list {
    display: flex;
    margin: -2px;
}
.radio-color__item {
    width: 24px;
    height: 24px;
    display: block;
    margin: 2px;
    cursor: pointer;

    input {
        visibility: hidden;
        position: absolute;
    }

    span {
        position: relative;
        width: 24px;
        height: 24px;
        display: block;
        border-radius: 12px;
        background-color: currentColor;
        transform: scale(.75);
        transition: transform .2s;

        &:before,
        &:after {
            display: block;
            position: absolute;
            content: '';
            border-radius: 1000px;
        }
        &:before {
            left: 3px;
            top: 3px;
            width: 18px;
            height: 18px;
            background-color: #fff;
            transform: scale(0);
            transition: transform .2s;
        }
        &:after {
            left: 7px;
            top: 7px;
            width: 10px;
            height: 10px;
            background-color: currentColor;
            transform: scale(0);
            transition: transform .2s;
        }
    }

    &:hover input:enabled ~ span {
        transform: scale(.8333);
    }
}
.radio-color__item input:enabled:checked {
    & ~ span,
    & ~ span:before,
    & ~ span:after {
        transform: scale(1);
    }
}
.radio-color__item--disabled {
    cursor: default;

    span {
        transform: scale(.75);

        &:before {
            transform: scale(1.33) rotateZ(45deg);
            left: 11px;
            top: -1px;
            width: 2px;
            height: calc(100% + 2px);
            background-color: #000;
            opacity: .3;
        }
    }
}
.radio-color__item--white {
    span {
        box-shadow: inset 0 0 0 2px #ccc;

        &:after {
            box-shadow: inset 0 0 0 2px #ccc;
        }
    }
}