/*
 * block-gallery
 */

@import '../variables';


.block-gallery {}
.block-gallery__body {
    position: relative;
}
.block-gallery__images {
    display: flex;
    flex-wrap: wrap;
    list-style: none;
    margin: 0;
    padding: 0;
}
.block-gallery__item {
    width: 50%;
    flex-shrink: 0;
    height: 360px;

    img {
        width: 100%;
        height: 360px;
        object-fit: cover;
    }
}
.block-gallery__aboutus {
    left: 50%;
    top: 50%;
    transform: translateX(-50%) translateY(-50%);
    position: absolute;
    width: 510px;
    max-width: 100%;
    padding: 35px 40px 37px;
    background-color: $block-gallery-card-bg-color;
    color: $block-gallery-card-font-alt-color;
}
.block-gallery__aboutus-header {
    color: $block-gallery-card-font-color;
    margin-bottom: 21px;
}