/*
 * button-remove
 */

@import '../variables';


.button-remove {
    background-color: transparent;
    border: none;
    width: 24px;
    height: 24px;
    padding: 7px;
    color: $button-remove-color;
    transition: color .2s;
    cursor: pointer;

    svg {
        fill: currentColor;
        display: block;
    }

    &:hover {
        color: $button-remove-hover-color;
    }
    &:focus {
        outline: none;
    }
}
.button-remove--lg {
    width: 32px;
    height: 32px;
    padding: 11px;
}