/*
 * cart
 */

@import '../mixins';


.cart {
    margin-bottom: 30px;
}
.cart__table {
    width: 100%;
    font-size: 15px;
}
.cart__header {
    font-size: 14px;
    font-weight: 600;
}



.cart__product-name {
    font-weight: 600;

    a {
        color: inherit;
        transition: color .15s;
    }
    a:hover {
        color: $card-link-hover-color;
        text-decoration: none;
    }
}
.cart__product-options {
    font-size: 13px;
    line-height: 19px;
    padding: 0;
    margin: 4px 0 0;
    list-style: none;
    color: $card-font-muted-color;
}





.cart-totals {

}
.cart-totals__table {
    width: 100%;
    margin-bottom: 26px;
    font-size: 15px;

    th, td {
        padding: 10px 0;
    }
    th {
        font-weight: 600;
        padding-right: 10px;
    }
    td {
        text-align: right;
    }

    tfoot {
        font-size: 20px;
    }

    tbody tr:first-child th,
    tbody tr:first-child td,
    tfoot tr:first-child th,
    tfoot tr:first-child td {
        border-top: 1px solid $card-divider-color;
    }
}
.cart-totals__button {
    width: 100%;
}

@media (min-width: 768px) {
    .cart {
        @include card();
    }
    .cart__body,
    .cart__footer {
        .cart__column {
            border-top: 1px solid $card-divider-color;
        }
    }
    .cart__column {
        padding: 15px;
    }
    .cart__column--image {
        text-align: center;
        width: 110px;

        img {
            width: 100%;
        }
    }
    .cart__column--quantity {
        padding-left: 80px;
        width: 1px;
        text-align: center;
    }
    .cart__column--remove {
        width: 1px;
        white-space: nowrap;
    }
    .cart__column--price,
    .cart__column--total {
        width: 170px;
        text-align: right;
    }
}

@media (min-width: 768px) and (max-width: 991px) {
    .cart__column--info,
    .cart__column--remove {
        padding-left: 0;
    }
    .cart__column--quantity,
    .cart__column--total {
        padding-left: 10px;
    }
    .cart__column--price,
    .cart__column--total {
        width: 1px;
        white-space: nowrap;
    }
}


@media (max-width: 767px) {
    .cart__table {
        display: block;
    }
    .cart__header {
        display: none;
    }
    .cart__body {
        display: block;

        tr {
            @include card();

            position: relative;
            display: block;
            padding: 20px;
            margin-bottom: 20px;
        }
    }
    .cart__footer {
        display: block;

        tr {
            @include card();

            display: flex;
            justify-content: space-between;
            padding: 15px;
            margin-bottom: 20px;
        }
    }
    .cart__column {
        display: block;
    }
    .cart__column--image {
        text-align: center;
        margin-bottom: 20px;

        img {
            max-width: 200px;
        }
    }
    .cart__column--info {
        margin: 0 -20px;
        text-align: center;
        border-bottom: 1px solid $card-divider-color;
        padding: 0 20px 18px;
        margin-bottom: 12px;
    }
    .cart__column--price,
    .cart__column--total {
        font-weight: 600;
    }
    .cart__column--price,
    .cart__column--quantity,
    .cart__column--total {
        padding: 6px 0;
        display: flex;
        align-items: center;
        text-align: right;
        justify-content: space-between;

        &:before {
            text-align: left;
            font-weight: 600;
            width: 55%;
            flex-shrink: 0;
            content: attr(data-title) ": ";
        }
    }
    .cart__column--remove {
        background-color: $card-bg-color;
        padding: 6px;
        position: absolute;
        right: 0;
        top: 0;
    }
}