/*
 * reviews-view
 */

@import '../variables';


.reviews-view__header {
    margin-bottom: 45px;
}
.reviews-view__form {
    margin-top: 40px;
    border-top: 1px solid $card-divider-color;
    padding-top: 60px;
}

@media (max-width: 575px) {
    .reviews-view__header {
        margin-bottom: 40px;
    }
    .reviews-view__form {
        margin-top: 30px;
        padding-top: 50px;
    }
}