/*
 * radio-select
 */

@import '../variables';


.radio-select {
    font-size: 13px;
    font-weight: 600;
    user-select: none;
}
.radio-select__list {
    display: flex;
    margin: -3px;
}
.radio-select label {
    margin: 3px;
    position: relative;
    display: block;
    font-size: inherit;
}
.radio-select input {
    position: absolute;
    opacity: 0;
    pointer-events: none;
}
.radio-select span {
    display: block;
    height: 28px;
    padding: 3px 12px;
    color: $radio-select-font-color;
    background-color: $radio-select-bg-color;
    border: 1px solid $radio-select-border-color;
    border-radius: 2px;
    cursor: pointer;
    transition: background-color .2s, border-color .2s;
}
.radio-select span:hover {
    color: $radio-select-hover-font-color;
    background-color: $radio-select-hover-bg-color;
    border: 1px solid $radio-select-hover-border-color;
    transition-duration: 0s, 0s;
}
.radio-select input:checked ~ span {
    color: $radio-select-active-font-color;
    background-color: $radio-select-active-bg-color;
    border: 1px solid $radio-select-active-border-color;
    transition-duration: .2s, .2s;
}
.radio-select input:disabled ~ span {
    cursor: default;
    color: $radio-select-disabled-font-color;
    background-color: $radio-select-disabled-bg-color;
    border: 1px solid $radio-select-disabled-border-color;
}