/*
 * widget-products-list
 */

@import '../variables';


.widget-products-list {
    padding: 0 0;
    margin: 0 -20px;
    list-style: none;
}
.widget-products-list__item {
    padding: 14px 20px;
    display: flex;
    border-bottom: 1px solid $card-divider-color;

    &:first-child {
        border-top: 1px solid $card-divider-color;
    }
}
.widget-products-list__image {
    width: 50px;
    margin-right: 14px;

    img {
        width: 100%;
    }
}
.widget-products-list__info {
    display: flex;
    flex-direction: column;
}
.widget-products-list__category {
    font-size: 11px;
    line-height: 13px;
    margin-bottom: 2px;
    margin-top: -3px;

    a {
        color: $card-font-muted-color;
        transition: color .15s;
    }
    
    a:hover {
        color: $card-link-hover-color;
        text-decoration: none;
    }
}
.widget-products-list__name {
    font-size: 13px;
    font-weight: 600;
    line-height: 15px;
    margin-bottom: 8px;
    flex-grow: 1;

    a {
        color: inherit;
        transition: color .15s;
    }

    a:hover {
        color: $card-link-hover-color;
        text-decoration: none;
    }
}
.widget-products-list__price {
    font-size: 13px;
    font-weight: 600;
    line-height: 15px;
    color: $price-default-color;
}
.widget-products-list__price-new {
    color: $price-new-color;
}
.widget-products-list__price-old {
    color: $price-old-color;
    font-weight: normal;
    text-decoration: line-through;
}