/*
 * toggle
 */

@import '../variables';


.toggle__title {
    $h-padding: 22px;
    $v-padding: 15px;

    display: block;
    margin-bottom: 0;

    button {
        padding: $v-padding ($h-padding * 2 + 9px) $v-padding $h-padding;
        font-size: 15px;
        line-height: 21px;
        font-weight: 600;
        font-family: inherit;
        color: inherit;
        letter-spacing: -.02em;
        width: 100%;
        background-color: transparent;
        border: none;
        text-align: left;
        cursor: pointer;
        fill: $details-arrow-color;
        position: relative;

        &:hover {
            background-color: $details-hover-bg-color;
        }
        &:focus {
            outline: none;
        }

        svg {
            position: absolute;
            top: calc(50% - 5px);
            right: $h-padding + 2px;
            transition: transform .2s;
        }
    }
}
.toggle__body {
    overflow: hidden;
    height: 0;
    opacity: 0;
    transition: height .2s, opacity .2s;
}
.toggle__content {
    border-top: 1px solid $details-divider-color;
    padding: 20px 22px;
    font-size: 15px;
    line-height: 22px;
}
.toggle--opened {
    .toggle__title button svg {
        transform: rotateZ(-90deg) translateX(-.5px) translateY(.5px);
    }
    .toggle__body {
        height: auto;
        opacity: 1;
    }
}