/*
 * product-gallery
 */

@import '../variables';

.product-gallery {

}
.product-gallery__carousel {
    margin-top: 20px;
}
.product-gallery__carousel-item {
    display: block;
    padding: 7px;
    box-shadow: inset 0 0 0 1px $card-divider-color;
}
.product-gallery__carousel-item--active {
    box-shadow: inset 0 0 0 2px $accent-color;
}