/*
 * block-collections
 */

@import '../mixins';
@import '../variables';


.block-collections {}
.block-collections__item--left {
    padding-right: 10px;
}
.block-collections__item--right {
    padding-left: 10px;
}
.block-collections__image {
    @include card();

    position: relative;
    padding: 15px;
    text-align: center;

    img {
        max-width: 100%;
    }
}
.block-collections__name {
    font-size: 20px;
    font-weight: 700;
    letter-spacing: -.03em;
    margin-bottom: 4px;
    backface-visibility: hidden;
}
.block-collections__description {
    font-size: 15px;
    line-height: 22px;
    color: $block-collections-description-color;
    margin-bottom: 20px;
}


@media (min-width: 768px) {
    .block-collections__info--top-left {
        border-left: 1px solid $block-collections-divider-color;
        margin-left: 35px;
        padding-left: 30px;
        padding-top: 15px;
        padding-bottom: 25px;

        margin-top: 30px;
    }
    .block-collections__info--bottom-right {
        border-left: 1px solid $block-collections-divider-color;
        margin-left: 35px;
        padding-left: 30px;
        padding-top: 20px;
        padding-bottom: 20px;

        margin-bottom: 30px;
    }
}


@media (max-width: 991px) {
    .block-collections__image {
        padding: 10px;
    }
    .block-collections__item--left {
        padding-right: 0;
    }
    .block-collections__item--right {
        padding-left: 0;
    }

    .block-collections__info--bottom-right {
        margin-left: 20px;
        padding-left: 25px;
    }
    .block-collections__info--top-left {
        margin-left: 20px;
        padding-left: 25px;
    }
}


@media (max-width: 767px) {
    .block-collections__item {
        display: flex;
        flex-direction: column;
    }
    .block-collections__info {
        order: 1;

        border-left: 1px solid $block-collections-divider-color;
        padding-top: 20px;
        padding-bottom: 20px;
        padding-right: 25px;
    }
}