/*
 * filter-list
 */

@import '../variables';


.filter-list {
    margin: 0;
    padding: 0;
    list-style: none;
    font-size: 14px;
    line-height: 16px;
}
.filter-list__row {
    margin: 0;
    display: flex;
    align-items: center;
    padding: 4px 0;
}
.filter-list__checkbox {
    width: 14px;
    height: 14px;
    margin-right: 6px;
}
.filter-list__name {
    font-size: 14px;
}
.filter-list__counter {
    font-size: (12 / 14 * 1em);
    color: $filter-counter-color;
}