/*
 * product
 */

@import '../variables';


.product__info {
    display: flex;
    flex-direction: row;
    padding: 40px;
}

.product__gallery {
    width: 50%;
    flex-shrink: 0;
    padding-right: 15px;
}
.product__details {
    width: 50%;
    flex-shrink: 0;
    padding-left: 15px;
}
.product__categories-sku {
    display: flex;
    justify-content: space-between;
    color: $card-font-muted-color;
    font-size: 13px;
    margin-bottom: 1px;

    a {
        color: inherit;
        transition: color .15s;
    }
    a:hover {
        color: $card-link-hover-color;
        text-decoration: none;
    }
}
.product__name {
    margin-bottom: 35px;
}
.product__rating {
    display: flex;
    color: $card-font-muted-color;
    font-size: 13px;
    line-height: 1;
    margin-bottom: 10px;

    a {
        color: inherit;
        transition: color .15s;
    }
    a:hover {
        color: $card-link-hover-color;
        text-decoration: none;
    }
}
.product__rating-stars {
    margin-right: 10px;
}
.product__description {
    font-size: 15px;
    line-height: 21px;
}


.product__price {
    font-size: 26px;
    font-weight: 700;
    margin: 20px 0 25px;
}
.product__price-new {
    color: $price-new-color;
}
.product__price-old {
    color: $price-old-color;
    font-weight: normal;
    font-size: 18px;
    text-decoration: line-through;
}
.product__options {

}
.product__option-label {
    font-size: 12px;
    line-height: 16px;
    font-weight: 700;
    text-transform: uppercase;
    color: $card-font-alt-muted-color;
    margin-bottom: 6px;
}

.product__actions {
    display: flex;
    flex-wrap: wrap;
    margin: -4px;
}
.product__actions-item {
    margin: 4px;
}
.product__quantity {
    width: 100px;
}
.product__wishlist,
.product__compare {
    display: flex;
}
.product__tags {
    margin-top: 15px;
    font-size: 13px;
    color: $card-font-muted-color;

    a {
        color: inherit;
        transition: color .15s;
    }
    a:hover {
        color: $card-link-hover-color;
        text-decoration: none;
    }
}
.product__share-links {
    margin-top: 25px;
}



.product__tabs {
    margin-top: 60px;
}

.product__tab-description,
.product__tab-reviews,
.product__tab-specification {
    margin: 60px 80px;
}

@media (min-width: 992px) and (max-width: 1199px) {
    .product__tab-description,
    .product__tab-reviews,
    .product__tab-specification {
        margin: 45px 60px;
    }
}
@media (min-width: 768px) and (max-width: 991px) {
    .product__tab-description,
    .product__tab-reviews,
    .product__tab-specification {
        margin: 35px 45px;
    }
}
@media (min-width: 576px) and (max-width: 767px) {
    .product__tab-description,
    .product__tab-reviews,
    .product__tab-specification {
        margin: 35px 40px;
    }
}
@media (max-width: 575px) {
    .product__tab-description,
    .product__tab-reviews,
    .product__tab-specification {
        margin: 20px 20px;
    }
}
@media (max-width: 991px) {
    .product__info {
        display: block;
    }
    .product__gallery {
        width: 100%;
        max-width: 480px;
        margin: 0 auto 20px;
        padding: 15px;
    }
    .product__details {
        width: auto;
        padding: 15px;
    }
}
@media (max-width: 767px) {
    .product__gallery {
        padding: 0;
        margin-bottom: 40px;
    }
    .product__details {
        padding: 0;
    }
}
@media (max-width: 575px) {
    .product__info {
        padding: 20px;
    }
    .product__tab-reviews {
        margin: 20px;
    }
    .product__categories-sku {
        display: block;
        margin-bottom: 20px;
    }
    .product__rating {
        display: block;
        margin-bottom: 16px;
    }
    .product__rating-stars {
        margin-bottom: 10px;
        margin-right: 0;
    }
}