/*
 * block-testimonials
 */

@import '../variables';


.block-testimonials {}
.block-testimonials__item {
    width: 100%;
    max-width: 480px;
    text-align: center;
}
.block-testimonials__icon {
    font-size: 29px;
    line-height: 30px;
    color: $testimonial-icon-color;
    margin-bottom: 24px;
}
.block-testimonials__author {
    font-weight: 600;
    margin-bottom: 12px;
}


.block-testimonials--dark {
    color: $testimonial-dark-text-alt-color;

    .block-testimonials__author {
        color: $testimonial-dark-text-color;
    }
}