/*
 * dropcart
 */

@import '../variables';


.dropcart {
    box-shadow: $dropcart-shadow;
    background: $dropcart-bg;
    color: $dropcart-font-color;
    list-style: none;
    margin: 0;
    width: 300px;
}
.dropcart__product {
    display: flex;
    padding: 15px 10px;
    border-bottom: 1px solid $dropcart-divider-color;
}
.dropcart__product-image {
    width: 60px;
    margin-right: 12px;

    img {
        max-width: 100%;
    }
}
.dropcart__product-info {
    flex-grow: 1;
}
.dropcart__product-name {
    font-size: 13px;
    font-weight: 600;
    line-height: 16px;

    a {
        color: inherit;
        transition: color .15s;
    }
    a:hover {
        color: $card-link-hover-color;
        text-decoration: none;
    }
}
.dropcart__product-options {
    margin: 4px 0 0;
    list-style: none;
    padding: 0;
    color: $dropcart-font-alt-color;
    font-size: 12px;
    line-height: 14px;
}
.dropcart__product-price {
    margin-top: 8px;
    font-size: 13px;
    font-weight: 600;
}
.dropcart__product-remove {
    margin-left: 10px;
}
.dropcart__totals {
    padding: 14px 15px 4px;
    font-size: 14px;
    line-height: 24px;

    table, td, th {
        padding: 0;
        margin: 0;
        border-spacing: 0;
        font-weight: 600;
    }
    table {
        width: 100%;
    }
    th, td {
        padding: 0 5px;
    }
    th {
        font-weight: normal;
        color: $dropcart-font-alt-color;
    }
    td {
        text-align: right;
    }
}
.dropcart__buttons {
    padding: 15px;
    display: flex;
}
.dropcart__buttons .btn {
    flex-basis: 0;
    flex-grow: 1;
}
.dropcart__buttons .btn + .btn {
    margin-left: 10px;
}