/*
 * slider
 */

@import '../variables';

.slider--with-dots .owl-carousel {
    .owl-dots {
        margin-top: 20px;
        display: flex;
        justify-content: center;
    }
    button.owl-dot {
        display: block;

        &:focus {
            outline: none;
        }
    }
    button.owl-dot span {
        display: block;
        width: 12px;
        height: 12px;
        margin: 5px;
        background-color: $slider-dot-color;
        border-radius: 6px;
    }
    button.owl-dot:hover span {
        background-color: $slider-dot-hover-color;
    }
    button.owl-dot.active span {
        background-color: $slider-dot-active-color;
    }
}

.slider--with-arrows .owl-carousel {
    .owl-nav button.owl-prev,
    .owl-nav button.owl-next {
        display: block;
        width: 34px;
        height: 37px;
        position: absolute;
        top: calc(50% - 18px);
        border-radius: 1.5px;
        background-color: $slider-button-bg-color;
        color: $slider-button-arrow-color;
        fill: currentColor;

        &:focus {
            outline: none;
        }
        &:not(.disabled):hover {
            background-color: $slider-button-hover-bg-color;
            color: $slider-button-hover-arrow-color;
        }
        &:not(.disabled):active {
            background-color: $slider-button-active-bg-color;
            color: $slider-button-active-arrow-color;
        }

        svg {
            position: absolute;
            display: block;
            left: calc(50% - 5px);
            top: calc(50% - 6.5px);
        }

        &.disabled {
            background-color: $slider-button-disabled-bg-color;
            color: $slider-button-disabled-arrow-color;
            cursor: default;
        }
    }
    .owl-nav button.owl-prev {
        left: -8px;
    }
    .owl-nav button.owl-next {
        right: -8px;

        svg {
            left: calc(50% - 3px);
        }
    }
}


.slider--dark {
    &.slider--with-dots .owl-carousel {
        button.owl-dot span {
            background-color: $slider-dot-dark-color;
        }
        button.owl-dot:hover span {
            background-color: $slider-dot-dark-hover-color;
        }
        button.owl-dot.active span {
            background-color: $slider-dot-dark-active-color;
        }
    }
}