/*
 * header
 */

@import '../variables';

.header {
    border-bottom: $header-border-bottom;
    background-color: $header-bg;
    color: $header-font-color;
}
.header__body {
    height: 70px;
    width: 1110px;
    margin: 0 auto;
    display: flex;
    position: relative;
    z-index: 10;
}
.header__mobilemenu {
    display: none;
    border: none;
    background-color: transparent;
    cursor: pointer;
    padding: 0 22px;
    fill: $header-menu-icon-color;

    svg {
        display: block;
    }

    &:hover {
        background-color: $header-item-hover-bg-color;
        fill: $header-menu-hover-icon-color;
    }
    &:focus {
        background-color: $header-item-focus-bg-color;
        fill: $header-menu-focus-icon-color;
        outline: none;
    }
}
.header__logo {
    display: flex;
    align-items: center;

    &, &:hover {
        color: inherit;
    }

    img,
    svg {
        display: block;
    }
}
.header__nav {
    margin-left: 30px;
}
.header__spring {
    flex-grow: 1;
}
.header__indicator {
    position: relative;
}
.header__indicator-button {
    height: 100%;
    border: none;
    background-color: transparent;
    display: flex;
    align-items: center;
    cursor: pointer;
    padding: 0 6px;

    &, &:hover {
        color: inherit;
    }
}
.header__indicator-dropdown {
    position: absolute;
    right: 0;
    top: 100%;
    visibility: hidden;
    transform-origin: top;
    transform: rotateX(45deg);
    opacity: 0;
    transition: transform .2s, opacity .2s;
}
.header__indicator:hover .header__indicator-button,
.header__indicator--opened .header__indicator-button {
    background-color: $header-item-hover-bg-color;
}
.header__indicator[data-dropdown-trigger="hover"]:hover,
.header__indicator[data-dropdown-trigger="click"].header__indicator--opened {
    .header__indicator-dropdown {
        opacity: 1;
        visibility: visible;
        transform: rotateX(0deg);
    }
}



@media (min-width: 992px) and (max-width: 1199px) {
    .header__body {
        width: 930px;
    }
}
@media (max-width: 991px) {
    .header__mobilemenu {
        display: block;
    }
    .header__logo {
        padding: 0 20px;
    }
}
@media (min-width: 768px) and (max-width: 991px) {
    .header__body {
        width: 690px;
    }
    .header__nav {
        display: none;
    }
}
@media (min-width: 576px) and (max-width: 767px) {
    .header__body {
        width: 510px;
    }
    .header__nav {
        display: none;
    }
}
@media (max-width: 575px) {
    .header__body {
        width: auto;
        height: 54px;
    }
    .header__mobilemenu {
        padding: 0 15px;
    }
    .header__logo {
        padding: 0 6px;
    }
    .header__nav {
        display: none;
    }
}