/*
 * typography
 */

@import '../variables';


.typography {
    line-height: 1.7;

    p {
        margin-bottom: 1.65rem;
    }

    figure {
        margin: 40px 0;
    }

    figcaption {
        color: $typography-figcaption-color;
        margin-top: 12px;
        font-size: 14px;
        text-align: center;
    }

    h1, h2, h3, h4 {
        margin-top: 1.4em;
        margin-bottom: .6em;
        backface-visibility: hidden;
    }

    h5, h6 {
        margin-top: 1.8em;
        margin-bottom: .8em;
    }
}