/*
 * main-nav
 */

@import '../variables';


.main-nav {
    height: inherit;
}
.main-nav__list {
    height: inherit;
    display: flex;
    padding: 0;
    margin: 0;
    list-style: none;
}
.main-nav__item--with--menu {
    position: relative;
}
.main-nav__sub-menu,
.main-nav__sub-megamenu {
    position: absolute;
    top: 100%;
    left: 0;
    visibility: hidden;
    transform-origin: top;
    transform: rotateX(45deg);
    opacity: 0;
    transition: transform .2s, opacity .2s;
}
.main-nav__item:hover {
    .main-nav__sub-menu,
    .main-nav__sub-megamenu {
        opacity: 1;
        visibility: visible;
        transform: rotateX(0deg);
    }
}
.main-nav__link {
    display: flex;
    height: 100%;
    font-size: 14px;
    font-weight: 600;
    padding: 0 16px;
    line-height: 66px;

    &, &:hover {
        color: inherit;
        text-decoration: none;
    }
}
.main-nav__link-arrow {
    align-self: center;
    margin-left: 6px;
    margin-bottom: 4px;
    fill: $header-arrow-color;
}
.main-nav__item:hover {
    .main-nav__link {
        background-color: $header-item-hover-bg-color;
    }
    .main-nav__link-arrow {
        fill: $header-item-hover-arrow-color;
    }
}