/*
 * alert
 */

@import '../variables';
@import '../mixins';


.alert {
    @include card();

    border: none;
    border-radius: 0;
    font-size: 14px;
    line-height: 18px;
    padding: 12px 20px;

    &::before {
        content: '';
        display: block;
        position: absolute;
        border-radius: 1.2px;
        top: 3px;
        left: 3px;
        width: 5px;
        height: calc(100% - 6px);
    }

    a {
        border-radius: 1.5px;
        color: inherit;
        padding: 0 3px;
        background-color: currentColor;
        border-bottom: 1px solid transparent;
        transition: border-bottom-color .12s;

        &:hover {
            color: inherit;
            text-decoration: none;
        }
    }
}
.alert-dismissible {
    padding-right: 42px;
}
.alert__close-button {
    position: absolute;
    top: 0;
    right: 0;
    border: none;
    padding: 0;
    background-color: transparent;
    cursor: pointer;
    width: 42px;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    fill: $alert-close-button-color;
    transition: fill .12s;

    &:hover {
        fill: $alert-close-button-hover-color;
    }
    &:focus {
        outline: none;
    }
    svg {
        display: block;
    }
}
.alert-sm {
    padding: 7px 17px;

    &.alert-dismissible {
        padding-right: 32px;
    }
    .alert__close-button {
        width: 32px;
    }
}
.alert-lg {
    padding: 15px 24px;
    font-size: 15px;

    &.alert-dismissible {
        padding-right: 48px;
    }
    .alert__close-button {
        width: 48px;
    }
}


@mixin alert($color, $font-color) {
    color: $font-color;

    &::before {
        background-color: $color;
    }
    a {
        background-color: rgba($font-color, .15);
    }
    a:hover {
        border-bottom-color: rgba($font-color, .5);
    }
    a:active {
        background-color: rgba($font-color, .25);
    }
}


.alert-primary {
    @include alert($alert-primary-color, $alert-primary-font-color);
}
.alert-success {
    @include alert($alert-success-color, $alert-success-font-color);
}
.alert-danger {
    @include alert($alert-danger-color, $alert-danger-font-color);
}
.alert-warning {
    @include alert($alert-warning-color, $alert-warning-font-color);
}