/*
 * post-card
 */

@import '../variables';
@import '../mixins';

.post-card {
    position: relative;
}
.post-card__image {
    position: relative;

    img {
        display: block;
        max-width: 100%;
    }
}
.post-card__info {

}
.post-card__date {
    font-size: 13px;
    line-height: 14px;
    margin-bottom: 8px;
    color: $card-font-muted-color;
}
.post-card__name {
    font-size: 15px;
    font-weight: 600;
    line-height: 20px;
}
.post-card__name-link {
    color: inherit;
    transition: color .4s;

    &:hover {
        transition-duration: .15s;
        text-decoration: none;
    }
}
.post-card__description {
    margin-top: 11px;
    font-size: 14px;
    line-height: 22px;
    color: $card-font-alt-muted-color;
}
.post-card__read-more {
    margin-top: 16px;
}


.post-card--layout--default {
    @include card();

    .post-card__info {
        padding: 20px 25px 28px;
    }
}
.post-card--layout--list {
    @include card();

    .post-card__description {
        overflow: hidden;
        max-height: (22px * 3);
    }

    @media (min-width: 768px) {
        display: flex;

        .post-card__image {
            width: 345px;
            flex-shrink: 0;
        }

        .post-card__info {
            padding: 25px 30px;
        }
    }

    @media (max-width: 767px) {
        .post-card__info {
            padding: 20px 25px 28px;
        }
    }
}
.post-card--layout--compact {
    .post-card__date {
        font-size: 12px;
        margin-top: 10px;
        margin-bottom: 5px;
    }
    .post-card__name {
        font-size: 13px;
        line-height: 17px;
    }
    .post-card__description,
    .post-card__read-more {
        display: none;
    }
}