/*
 * indicator
 */

@import '../variables';

.indicator {
    &:focus {
        outline: none;
    }
}
.indicator__area {
    display: block;
    padding: 8px;
    position: relative;
    fill: currentColor;
}
.indicator__value {
    position: absolute;
    right: 0;
    top: 0;
    background-color: $accent-color;
    color: $accent-opposite-color;
    font-size: 10px;
    font-weight: 600;
    line-height: 1;
    padding: 2px 5px 3px;
    border-radius: 1000px;
    text-align: center;
    min-width: 15px;
}
.indicator__icon {
    display: block;
}
